import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { NotificationItem, NotificationResponse } from '../models/notification.item.model';


@Injectable({
  providedIn: 'root'
})
export class NotificationsService {
  private headers: HttpHeaders;
  private url: string = environment.url;
  private accessToken: string | undefined;

  constructor(private http: HttpClient) {
    this.accessToken = sessionStorage.getItem('Token')?.toString();

    this.headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'accept': 'application/json',
      'x-access-token': this.accessToken!
    });
  }

  getAllNotifications(): Promise<NotificationResponse | undefined> {
    const options = { headers: this.headers};
    return this.http.get<NotificationResponse>(`${this.url}/notification/notifications`, options).toPromise();
  }

  createScheduleNotificacion(notification: NotificationItem): Promise<NotificationResponse | undefined> {
    const options = { headers: this.headers};
    return this.http.post<NotificationResponse>(`${this.url}/notification/notifications`,notification, options,).toPromise();
  }

  createInstantNotificacion(notification: NotificationItem): Promise<NotificationResponse | undefined> {
    const options = { headers: this.headers};
    return this.http.post<NotificationResponse>(`${this.url}/notification/notifications/instant`,notification, options,).toPromise();
  }

  deleteNotification(id: number): Promise<NotificationResponse | undefined> {
    const options = { headers: this.headers};
    return this.http.delete<NotificationResponse>(`${this.url}/notification/notifications/${id}`, options).toPromise();
  }

  updateNotification(notification: NotificationItem): Promise<NotificationResponse | undefined> {
    const options = { headers: this.headers};
    return this.http.put<NotificationResponse>(`${this.url}/notification/notifications`,notification, options).toPromise();
  }
}
