import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NotificationItem } from 'src/app/models/notification.item.model';
import { KeyValue } from '@angular/common';
import { NotificationsService } from 'src/app/services/notifications.service';
import { UtilsService } from 'src/app/services/utils.service';
import { EventService } from 'src/app/services/event.service';

@Component({
  selector: 'app-admin-notificaciones',
  templateUrl: './admin-notificaciones.component.html',
  styleUrls: ['./admin-notificaciones.component.css'],
})
export class AdminNotificacionesComponent implements OnInit {
  constructor(
    private router: Router,
    private notificationsService: NotificationsService,
    private utilsService: UtilsService,
    private eventService: EventService
  ) {
    this.categoriesHeading = Object.keys(this.listHeading[0]);
  }

  renderList: any[] = [];

  notificationsList: NotificationItem[] = [];

  nextId = 1;
  eventAndPromoList: any[] = [];

  listHeading: {}[] = [
    {
      'Nombre de la notificación': '',
      'Fecha de creación': '',
      'Fecha de envío': '',
      Evento: '',
      'Usuarios Enviados': '',
      Estatus: '',
      '': '',
    },
  ];

  categoriesHeading: string[] = [];

  filtro = '';

  isShowDiv: boolean = true;

  originalOrder = (
    a: KeyValue<string, string>,
    b: KeyValue<string, string>
  ): number => {
    return 0;
  };

  ngOnInit(): void {
    this.loadActiveEvents();
  }

  public getTypeOf(variable: any): string {
    return typeof variable;
  }

  async loadNotifications() {
    this.renderList = [];
    try {
      const response = await this.notificationsService.getAllNotifications();
      if (response?.success === true) {
        this.notificationsList = response.result;

        this.notificationsList = this.notificationsList.reverse();

        response.result.forEach((element) => {
          this.renderList.push({
            'Nombre de la notificación': element.subject,
            'Fecha de creación': element.createdAt ?? '',
            'Fecha de envío': element.dateSend ?? '',
            Evento:
              this.getEventName(element.eventId) == ''
                ? 'Evento no existente'
                : this.getEventName(element.eventId),
            'Usuarios enviados': '123',
            Estatus: this.getNotificationStatus(element.notificationType),
            id: element.id,
          });
        });
      } else {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        );
      }
    } catch (error) {
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'Por favor vuelve a intentar más tarde.'
      );
    }
  }

  getEventName(id: number) {
    let event = this.eventAndPromoList.find((e) => e.id === id);
    return event ? event.name : '';
  }

  getNotificationStatus(status: string) {
    switch (status) {
      case 'sent':
        return 'Enviado';
        break;
      case 'draft':
        return 'Borrador';
      case 'scheduled':
        return 'Programado';
        break;
      default:
        return '';
        break;
    }
  }

  async loadActiveEvents() {
    this.eventService.getEvents().subscribe({
      next: async (res) => {
        res.result.map((e: any) => {
          if (e.status === 'active') {
            this.eventAndPromoList.push({
              id: e.id,
              name: e.title,
              type: 'event',
            });
          }
        });

        this.loadNotifications();
      },
      error: (e) =>
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        ),
    });
  }

  showActions(event: any, index: number) {
    this.isShowDiv = true;
    var element = document.getElementById('item' + index);
    var listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if ('item' + index !== listItes?.id || !listItes.id) {
      element?.classList.add('actions-disble');
    }
  }

  async deleteNotification(notification: any) {
    try {
      const response = await this.notificationsService.deleteNotification(
        notification.id
      );
      if (response?.success === true) {
        // TODO: CAMBIAR ESTA CONDICIÓN CUANDO SE PUEDA ELIMINAR UNA NOTIFICACIÓN O BACK RETORNE UN MENSAJE DE ERROR O CÓDIGO
        if (
          response?.message?.description ===
          'Una notificacion enviada no se puede modificar o eliminar'
        ) {
          this.utilsService.dialog(
            'ERROR',
            'Error',
            'Una notificación enviada no se puede modificar o eliminar.'
          );
          return;
        } else {
          this.utilsService.dialog(
            'SUCCESS',
            'Notificación',
            response?.message.description
          );
        }
      } else {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        );
      }
    } catch (error) {
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'Por favor vuelve a intentar más tarde.'
      );
    }

    this.loadNotifications();
  }

  editNotification(index: number) {
    let notificationModel = this.notificationsList[index];
    console.log(notificationModel);
    this.router.navigate(['/admin/edit-notificacion'], {
      queryParams: { NotificationModel: JSON.stringify(notificationModel) },
    });
  }

  goToPage(pageName: string) {
    this.router.navigate([`${pageName}`]);
  }
}
