import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
    providedIn: 'root'
})

export class SpeakerService {
    private headers: {} = {};
    private url: string = environment.url;
    private accessToken: string | undefined;

    constructor(private http: HttpClient) {
        this.accessToken = sessionStorage.getItem('Token')?.toString();

        this.headers = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'accept': 'application/json',
                'x-access-token': this.accessToken!
            })
        };
    }

    // getAllSpeakerByEvent(page: number, size: number, event_id: number): Promise<any> {
    //     return this.http.get(`${this.url}eventos/speakers/all-by-event/?page=${page}&size=${size}&eventId=${event_id}`, this.headers).toPromise();
    // }

    // Obtener conferencista del evento
    getSpeakerByEvent(event_id: number): Promise<any> {
        return this.http.get(`${this.url}eventos/speakers/event/${event_id}`, this.headers).toPromise();
    }

    // Obtener un conferencista
    getOneSpeaker(speaker_id: number): Promise<any> {
        return this.http.get(`${this.url}eventos/speakers/${speaker_id}`, this.headers).toPromise();
    }

    // Crear un conferencista
    addSpeakerToEvent(data: any): Promise<any> {
        // return this.http.post(`${baseUrl}eventos/speakers`, data, httpOptionsVerification);
        return this.http.post(`${this.url}eventos/speakers/back/register-speaker/`, data, this.headers).toPromise();
    }

    // Crear un conferencista
    addSpeaker(data: any): Promise<any> {
        return this.http.post(`${this.url}eventos/speakers/`, data, this.headers).toPromise();
    }

    // Actualiar un conferencista de un evento (+ asistencia/registro)
    updateSpeakerByEvent(ID: number, data: any): Promise<any> {
        return this.http.put(`${this.url}eventos/speakers/${ID}`, data, this.headers).toPromise();
    }

    // Eliminar un conferencista de un evento
    deleteSpeaker(ID: number): Promise<any> {
        return this.http.delete(`${this.url}eventos/speakers/${ID}`, this.headers).toPromise();
    }
}
