import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { PromotionResponse } from '../models/promotions-models';
import { AssociatedConstanceItemReport } from '../models/associated-constance-model';
import { SearchReportParamsModel } from '../models/search-report-params-model';
import { GenericApiResponse } from '../models/generic-api-response.model';
import { PointsCertificateModel } from '../models/points-certificate-model';
import { SurveyReportModel } from '../models/survey-report-model';

@Injectable({
  providedIn: 'root'
})
export class CertificatesService {
  private headers: HttpHeaders;
  private url: string = environment.url;
  private accessToken: string | undefined;

  constructor(private http: HttpClient) {
    this.accessToken = sessionStorage.getItem('Token')?.toString();

    this.headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'accept': 'application/json',
      'x-access-token': this.accessToken!
    });
  }

  getAllAssociatedCertificateItems(associatedParams: SearchReportParamsModel): Promise<GenericApiResponse<AssociatedConstanceItemReport> | undefined> {
    const params = new HttpParams()
      .set('name', associatedParams.name)
      .set('page', associatedParams.page)
      .set('size', associatedParams.size)
      .set('start_at', associatedParams.startDate)
      .set('end_at', associatedParams.endDate)
      .set('status', associatedParams.status)

    const options = { headers: this.headers, params: params };

    //TODO: ACTUALIZAR ESTE ENDPOINT CUANDO SE TENGA EL BACKEND

    return this.http.get<GenericApiResponse<AssociatedConstanceItemReport>>(`${this.url}/promo/promo/for-admin`, options).toPromise();
  }

  getAllPointsCertificatesItems(associatedParams: SearchReportParamsModel): Promise<GenericApiResponse<PointsCertificateModel> | undefined> {
    const params = new HttpParams()
      .set('name', associatedParams.name)
      .set('page', associatedParams.page)
      .set('size', associatedParams.size)
      .set('start_at', associatedParams.startDate)
      .set('end_at', associatedParams.endDate)
      .set('status', associatedParams.status)

    const options = { headers: this.headers, params: params };

    //TODO: ACTUALIZAR ESTE ENDPOINT CUANDO SE TENGA EL BACKEND

    return this.http.get<GenericApiResponse<PointsCertificateModel>>(`${this.url}/promo/promo/for-admin`, options).toPromise();
  }


  getAllSurveryReportItems(associatedParams: SearchReportParamsModel): Promise<GenericApiResponse<SurveyReportModel> | undefined> {
    const params = new HttpParams()
      .set('name', associatedParams.name)
      .set('page', associatedParams.page)
      .set('size', associatedParams.size)
      .set('start_at', associatedParams.startDate)
      .set('end_at', associatedParams.endDate)
      .set('status', associatedParams.status)

    const options = { headers: this.headers, params: params };

    //TODO: ACTUALIZAR ESTE ENDPOINT CUANDO SE TENGA EL BACKEND

    return this.http.get<GenericApiResponse<SurveyReportModel>>(`${this.url}/promo/promo/for-admin`, options).toPromise();
  }


  


}
