import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart } from '@angular/router';
import { RouterModule } from '@angular/router';

interface Menu {
  id: string,
  src: string,
  routerLink: string
}


@Component({
  selector: 'app-main-menu',
  templateUrl: './main-menu.component.html',
  styleUrls: ['./main-menu.component.css'],
})
export class MainMenuComponent implements OnInit {
  showHead: boolean = false;
  public main_menu: Menu[] = [];

  constructor(private router: Router) {
    // on route change to '/login', set the variable showHead to false
    router.events.forEach((event) => {
      if (event instanceof NavigationStart) {
        if (
          event['url'] == '/login' ||
          event['url'] == '/recovery' ||
          event['url'] == '/code-recovery' ||
          event['url'] == '/new-password'
        ) {
          this.showHead = false;
        } else {
          this.showHead = true;
        }
      }
    });
  }

  ngOnInit(): void {
    const userRole: string | boolean = sessionStorage.getItem('Role') || false;

    if (userRole === 'ROLE_SUPERADMIN' || userRole === 'ROLE_ADMIN') {
      this.main_menu = [
        {
          id: 'users',
          routerLink: 'users', // 'users/admin-empresas', -> llega con ambos activos pero luego pierde el active del padre
          src: '../../assets/images/UsersIcon.svg'
        },
        {
          id: 'evento',
          routerLink: 'events', // 'events/calendario-eventos', -> llega con ambos activos pero luego pierde el active del padre
          src: '../../assets/images/EventoIcon.svg'
        },
        {
          id: 'reportes',
          routerLink: 'reportes',
          src: '../../assets/images/ReporteIcon.svg'
        },
        {
          id: 'notificaciones',
          routerLink: 'notificaciones',
          src: '../../assets/images/NotificacionIcon.svg'
        },
        {
          id: 'promociones',
          routerLink: 'admin-promociones',
          src: '../../assets/images/promotion_icon.svg'
        }
      ]
    } else if (userRole === 'ROLE_ORGANIZADOR') {
      this.main_menu = [
        {
          id: 'evento',
          routerLink: 'events/calendario-eventos',
          src: '../../assets/images/EventoIcon.svg'
        },
        {
          id: 'reportes',
          routerLink: 'reportes',
          src: '../../assets/images/ReporteIcon.svg'
        }
      ]
    } else {
      // ROLE_ALIADO 
      this.main_menu = [
        {
          id: 'promociones',
          routerLink: 'admin-promociones',
          src: '../../assets/images/promotion_icon.svg'
        }
      ]
    }
  }

  logout() {
    sessionStorage.removeItem('Token');
    sessionStorage.clear();
    localStorage.clear();
    this.router.navigate([`/login`]);
  }
}
