import { AssociatedConstanceItemReport } from "src/app/models/associated-constance-model";
import { GenericApiResponse } from "src/app/models/generic-api-response.model";
import { PointsCertificateModel } from "src/app/models/points-certificate-model";
import { Promotion, PromotionResponse } from "src/app/models/promotions-models";

export function getPointsCertificateMock(count: number): GenericApiResponse<PointsCertificateModel[]> {
    return {
        success: true,
        message: {
          title: 'Success',
          description: 'Poinst Certificate retrieved successfully',
        },
        result: generateItems(count),
      }
}

function generateItems(count: number): PointsCertificateModel[] {
    const items: PointsCertificateModel[] = [];
  
    let id = 1;
    for (let i = 0; i < count; i++) {
      const sector = `Sector ${i + 1}`
      const comision = `Comisión ${i + 1}`
      const companyName = `Company ${i}`;
      const Nit = `Category ${Math.floor(Math.random() * 5) + 1} - A`
      const certificateNumber =  `Category ${Math.floor(Math.random() * 5) + 1} - B`
      const mainContact = `Contacto ${i + 1}`
      const creationDate = '2022-01-01';
      const accumulatePoints = Math.floor(Math.random() * 5) + 1
      const quetzalsTotal = Math.floor(Math.random() * 5) + 1
  
  
      const itemReport: PointsCertificateModel = {
        id,
        sector,
        comision,
        companyName,
        Nit,
        certificateNumber,
        creationDate,
        mainContact, 
        accumulatePoints,
        quetzalsTotal
      };
      
      items.push(itemReport);
      id++;
    }
  
    return items;
  }

  
  