import { Router } from '@angular/router';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'; // ModalDismissReasons,
import { Component, ViewEncapsulation, Input } from '@angular/core';

@Component({
  selector: 'app-dialog',
  // standalone: true, // Ayuda a no tener que declara la ruta en el module
  templateUrl: './dialog.component.html',
  styleUrls: ['./dialog.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class DialogComponent {
  @Input() type?: string;
  @Input() page?: string;
  @Input() title?: string;
  @Input() icono?: string;
  @Input() message?: string;
  @Input() codeValue!: string;

  constructor(
    private router: Router,
    private modalService: NgbModal,
    public activeModal: NgbActiveModal
  ) { }

  close() {
    this.modalService.dismissAll(this);
    (this.page) && ( this.goToPage(this.page) );
  }

  goToPage(pageName: string) {
    this.router.navigate([`${pageName}`]);
  }

  // closeDialog() {
  //   this.modalService.dismissAll(this);
  //   if(this.page)
  //   {
  //     this.goToPage(this.page ? this.page : "");
  //   }    
  // }
}
