import { Component, OnInit } from '@angular/core';
import { KeyValue } from '@angular/common';
import { Router } from '@angular/router';
import { BackServiceUsers } from 'src/app/models/back-service.model';
import { BackofficeService } from 'src/app/services/backoffice.service';
import { NgbNavModule, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DialogComponent } from 'src/app/Extra/dialog/dialog.component';

@Component({
  selector: 'app-edit-stands',
  templateUrl: './edit-stands.component.html',
  styleUrls: ['./edit-stands.component.css']
})
export class EditStandsComponent implements OnInit {

  accessToken:any;
  active = 1;
  fileLogo: string = '';
  fileLevel1: string = ''; fileLevel2: string = ''; fileLevel3: string = ''; fileLevel4: string = '';

  constructor(private router: Router, private backofficeService: BackofficeService, private modalService: NgbModal) {
   }

  ngOnInit(): void {
  }

  goToPage(pageName:string){
    this.router.navigate([`${pageName}`]);
  }

  onFileSelected(event: any){

    if (event.target.files && event.target.files.length > 0) {

      if(event.target.id === "fileL1")
          this.fileLevel1 = event.target.files[0].name;
      else if(event.target.id === "fileL2")
        this.fileLevel2 = event.target.files[0].name;
      else if(event.target.id === "fileL3")
        this.fileLevel3 = event.target.files[0].name;
      else if(event.target.id === "fileL4")
        this.fileLevel4 = event.target.files[0].name;
      else
        this.fileLogo = event.target.files[0].name;
    }
    else
    {
      if(event.target.id === "fileL1")
        this.fileLevel1 = "No hay archivos seleccionados.";
      else if(event.target.id === "fileL2")
        this.fileLevel2 = "No hay archivos seleccionados.";
      else if(event.target.id === "fileL3")
        this.fileLevel3 = "No hay archivos seleccionados.";
      else if(event.target.id === "fileL4")
        this.fileLevel4 = "No hay archivos seleccionados.";
      else
        this.fileLogo = "No hay archivos seleccionados.";
    }
  }

  open() {
    const modalRef = this.modalService.open(DialogComponent, {centered: true});
  }

}
