import { Component, OnInit } from '@angular/core';
// import { FormControl } from '@angular/forms';
import { KeyValue } from '@angular/common';
import { NgbDate, NgbCalendar, NgbDateParserFormatter, NgbDatepickerModule, NgbDateStruct, NgbAlertModule } from '@ng-bootstrap/ng-bootstrap';
import { UtilsService } from 'src/app/services/utils.service';
import { ConferenceService } from 'src/app/services/conference.service';

interface Activity {
  id: number;
  lugar: string;
  enlace: string
  eventId: number;
  direccion: string;
  modalityId: number;
  modalidadID: number;
  conferencista: string,
  nombreActividad: string;
  fechaHoraFin: string;
  fechaHoraInicio: string;
  horaFin: string,
  horaInicio: string,
  fechaFin: NgbDateStruct,
  fechaInicio: NgbDateStruct,
}
@Component({
  selector: 'app-edit-conferencias',
  templateUrl: './edit-conferencias.component.html',
  styleUrls: ['./edit-conferencias.component.css']
})
export class EditConferenciasComponent implements OnInit {
  model!: NgbDateStruct;
  model2!: NgbDateStruct;
  fileCalendar: string = '';
  fileGafete: string = '';
  isShowDiv: boolean = true;

  list: {}[] = [
    { "Nombre del Usuario": "Usuario 1", "Pregunta": "Pregunta ABC", "Correo electrónico": "info@gmail.com", "Visibilidad": "Activo" }
  ];
  categoriesHeading: string[] = [];

  public eventID: number = 0;
  public activityID: number = 0;
  public activities: any[] = [];
  public loading: boolean = false;
  public disabled: boolean = false;
  public modalityActivity: any[] = [];
  public accessToken: string | null = '';
  public activity: Activity = {} as Activity;


  constructor(
    private utilsService: UtilsService,
    private conferenceService: ConferenceService
  ) { this.eventID = Number(sessionStorage.getItem('EventID')) }

  async ngOnInit(): Promise<void> {
    this.isShowDiv = true;
    const token: string | undefined = sessionStorage.getItem('Token')?.toString();
    this.activityID = Number(this.utilsService.getParam('ID'));

    if (token && this.eventID > 0 && this.activityID == 0) {
      await this.loadModality();
      this.activity.id = 0;
      this.activity.eventId = this.eventID;
    }
    else if (token && this.eventID > 0 && this.activityID > 0) {
      await this.loadModality();
      await this.getOneActivity(this.activityID);
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', '¡No tienes acceso!');
      this.goToPage('/login');
    }
  }

  originalOrder = (a: KeyValue<string, string>, b: KeyValue<string, string>): number => {
    return 0;
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName)
  }

  // ELiminar 
  onFileSelected(event: any) {
    console.log(event.target.id);
    if (event.target.files && event.target.files.length > 0) {

      if (event.target.id === "fileCal")
        this.fileCalendar = event.target.files[0].name;
      else
        this.fileGafete = event.target.files[0].name;
    }
    else {
      if (event.target.id === "fileCal")
        this.fileCalendar = "No hay archivos seleccionados.";
      else
        this.fileGafete = "No hay archivos seleccionados.";
    }
  }

  isNormal(dataValue: any) {
    if (dataValue === "Visibilidad") {
      return false;
    }
    else
      return true;
  }

  isRegistro(dataValue: any) {
    if (dataValue === "Visibilidad") {
      return true;
    }
    else
      return false;
  }

  showActions(event: any, index: number) {
    this.isShowDiv = true;
    var element = document.getElementById('item' + index);
    var listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if (('item' + index) !== listItes?.id || !listItes.id) {

      element?.classList.add('actions-disble');
    }

  }
  // Fin ELiminar 

  loadModality() {
    this.utilsService.getModalities().then(
      (response) => {
        this.modalityActivity = response.result.modalities;
      },
      error => {
        console.log(error)
      }
    )
  }

  getOneActivity(activity_id: number) {
    this.loading = true;
    this.conferenceService.getOneActivity(activity_id).then(
      (response) => {
        console.log(response)
        const tempEnd = (response.result.fechaHoraFin.substr(0, 10)).split('-');
        const tempStar = (response.result.fechaHoraInicio.substr(0, 10)).split('-');

        this.activity.eventId = this.eventID;
        this.activity.id = response.result.id;
        this.activity.lugar = response.result.direccion;
        // this.activity.direccion = response.result.direccion;
        this.activity.modalityId = response.result.modalityId;
        // this.activities.modalidadID: response.result.modalityId;
        this.activity.nombreActividad = response.result.nombreActividad;
        this.activity.horaFin = response.result.fechaHoraFin.substr(11, 5);
        this.activity.horaInicio = response.result.fechaHoraInicio.substr(11, 5);
        this.activity.fechaFin = { day: Number(tempEnd[2]), month: Number(tempEnd[1]), year: Number(tempEnd[0]) };
        this.activity.fechaInicio = { day: Number(tempStar[2]), month: Number(tempStar[1]), year: Number(tempStar[0]) };
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Error al cargar actividad!');
        this.goToPage('/admin/events/admin-agenda');
      }).finally(() => {
        this.loading = false
      })
  }

  async processData() {
    this.activity.direccion = this.activity.lugar;
    this.activity.modalityId = Number(this.activity.modalityId);
    this.activity.modalidadID = Number(this.activity.modalityId);
    this.activity.fechaHoraFin = `${this.activity.fechaFin.year}-${this.activity.fechaFin.month}-${this.activity.fechaFin.day} ${this.activity.horaFin}`;
    this.activity.fechaHoraInicio = `${this.activity.fechaInicio.year}-${this.activity.fechaInicio.month}-${this.activity.fechaInicio.day} ${this.activity.horaInicio}`;

    if (this.activity.id === 0) {
      await this.save(this.activity);
    } else {
      await this.update(this.activity);
    }

    this.loading = false;
    this.disabled = false;
  }

  save(activity: Activity) {
    console.log('SAVE ', activity);
    this.loading = true;
    this.disabled = true;

    this.conferenceService.createActivity(activity).then(
      (response) => {
        console.log(response);
        this.utilsService.dialog('SUCCESS', 'Exito', '¡Actividad registrada!');
        this.goToPage('/admin/events/admin-conferencias');
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Al registrar actividad!');
      }).finally(() => {
        this.loading = false
        this.disabled = false;
      });
  }

  update(activity: Activity) {
    console.log('UPDATE ', activity);
    this.loading = true;
    this.disabled = true;
    
    this.conferenceService.editActivity(activity.id, activity).then(
      (response) => {
        console.log(response);
        this.utilsService.dialog('SUCCESS', 'Exito', '¡Actividad actualizada!');
        this.goToPage('/admin/events/admin-conferencias');
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Al registrar actividad!');
      }).finally(() => {
        this.loading = false
        this.disabled = false;
      });
  }

}
