import { Component, OnInit, ViewChild } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { DialogComponent } from 'src/app/Extra/dialog/dialog.component';
import { ActivatedRoute, Router } from '@angular/router';
import { BackServiceUsers, BackServiceCompanies, listas } from 'src/app/models/back-service.model';
import { BackofficeService } from 'src/app/services/backoffice.service';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { ErrorDialogComponent } from 'src/app/Extra/error-dialog/error-dialog.component';
import { NgForm } from '@angular/forms';
import { CommunicationChannelsResultModel } from 'src/app/models/communication-channels.model';

@Component({
  selector: 'app-edit-usuario',
  templateUrl: './edit-usuario.component.html',
  styleUrls: ['./edit-usuario.component.css']
})
export class EditUsuarioComponent implements OnInit {
  @ViewChild('formulario') formulario!: NgForm;

  accessToken: any;
  name: string = "";
  secondName: string = "";
  lastName: string = "";
  secondLastname: string = "";
  userRole: string = ""; puesto: string = ""; userArea: string = "";
  countryID: string = ""; companyNIT: string = "";
  comite: string = ""; comision: string = ""; genderID: string = "";
  userData = new BackServiceUsers();
  companyData = new BackServiceCompanies();
  testPerm = "escanear eventos";
  model!: NgbDateStruct;
  userID: any;

  jobList: listas[] = [];
  areaList: listas[] = [];
  comiteList: listas[] = [];
  countryList: listas[] = [];
  comisionList: listas[] = [];

permissions: string[] = [];
escanear_eventos: boolean = false;
subir_promociones: boolean = false;
descargar_reporte_asistente: boolean = false;
constancia_asociado: boolean = false;
certificado_puntos: boolean = false;

communicationsChannel: CommunicationChannelsResultModel | undefined;

  roleList = [
    {
      id: 'superadmin',
      name: 'Administrador - All Access'
    },
    {
      id: 'admin',
      name: 'Administrador'
    },
    {
      id: 'colaborador',
      name: 'Colaborador'
    },
    {

      id: 'aliado_comercial',
      name: 'Aliado Comercial'
    },
    {

      id: 'organizador_eventos',
      name: 'Organizador de Eventos'
    },
    {

      id: 'asociado',
      name: 'Asociado'
    },
    {

      id: 'noasociado',
      name: 'No Asociado'
    }
  ];


  constructor(
    private router: Router,
    private modalService: NgbModal,
    private Activatedroute: ActivatedRoute,
    private backofficeService: BackofficeService) { }

  async ngOnInit() {
    await this.loadLists();
    this.userID = this.Activatedroute.snapshot.queryParams['ID'] || 0;

    if (sessionStorage.getItem('Token') && this.userID > 0) {
      // EDIT
      this.accessToken = sessionStorage.getItem('Token');
      this.loadDatabyID();
    }
    else if (sessionStorage.getItem('Token')) {
      // NEW
      this.userData.roles = 'noasociado';
      this.accessToken = sessionStorage.getItem('Token');
    }
    else {
      // NO ACCESS
      alert("No tienes acceso...");
      this.goToPage('/login');
    }
  }

  role(name: string) {
    switch (name) {
      case 'ROLE_SUPERADMIN':
        return 1;
        break;
      case 'ROLE_ALIADO_COMERCIAL':
        return 2;
        break;
      case 'ROLE_COLABORADOR':
        return 3;
        break;
      case 'ROLE_ASOCIADO':
        return 4;
        break;
      case 'ROLE_NOASOCIADO':
        return 5;
        break;
      default:
        return 5;
        break;
    }
  }

  setPermissions() {
    this.escanear_eventos = this.permissions.includes('escanear_eventos');
    this.subir_promociones = this.permissions.includes('subir_promociones');
    this.descargar_reporte_asistente = this.permissions.includes('descargar_reporte');
    this.constancia_asociado = this.permissions.includes('constancia_asociado');
    this.certificado_puntos = this.permissions.includes('certificado_puntos');
  }

  onPermissionChange($event: any) {
    if ($event.target.name == 'escanear_eventos' && $event.target.checked) {
      this.permissions.push('escanear_eventos');
    } else if ($event.target.name == 'escanear_eventos' && !$event.target.checked) {
      this.permissions.splice(this.permissions.indexOf('escanear_eventos'), 1);
    }
    if ($event.target.name == 'subir_promociones' && $event.target.checked) {
      this.permissions.push('subir_promociones');
    } else if ($event.target.name == 'subir_promociones' && !$event.target.checked) {
      this.permissions.splice(this.permissions.indexOf('subir_promociones'), 1);
    }
    if ($event.target.name == 'descargar_reporte' && $event.target.checked) {
      this.permissions.push('descargar_reporte');
    } else if ($event.target.name == 'descargar_reporte' && !$event.target.checked) {
      this.permissions.splice(this.permissions.indexOf('descargar_reporte'), 1);
    }
    if ($event.target.name == 'constancia_asociado' && $event.target.checked) {
      this.permissions.push('constancia_asociado');
    } else if ($event.target.name == 'constancia_asociado' && !$event.target.checked) {
      this.permissions.splice(this.permissions.indexOf('constancia_asociado'), 1);
    }
    if ($event.target.name == 'certificado_puntos' && $event.target.checked) {
      this.permissions.push('certificado_puntos');
    } else if ($event.target.name == 'certificado_puntos' && !$event.target.checked) {
      this.permissions.splice(this.permissions.indexOf('certificado_puntos'), 1);
    }

  }

  successDialog(pageUrl:string, type: string) {
    const modalRef = this.modalService.open(DialogComponent, {centered: true});
    modalRef.componentInstance.page = pageUrl;
    modalRef.componentInstance.type = type;
  }



  errorDialog(errorMessage: string) {
    const modalRef = this.modalService.open(ErrorDialogComponent, { centered: true })
    modalRef.componentInstance.errorMessage = errorMessage;
  }

  goToPage(pageName: string) {
    this.router.navigate([`${pageName}`]);
  }

  loadLists() {
    this.backofficeService.getCountries()
      .subscribe({
        next: (res) => {
          this.countryList = res.result.countries;
          // console.log(this.countryList);
        },
        error: (e) => {
          this.errorDialog(e.error.message.description);
        }
      })

    this.backofficeService.getPuestos()
      .subscribe({
        next: (res) => {
          this.jobList = res.result.puestos;
        },
        error: (e) => {
          this.errorDialog(e.error.message.description);
        }
      })

    this.backofficeService.getAreas()
      .subscribe({
        next: (res) => {
          this.areaList = res.result.areas;

        },
        error: (e) => {
          this.errorDialog(e.error.message.description);
        }
      })

    this.backofficeService.getComisiones()
      .subscribe({
        next: (res) => {
          this.comisionList = res.result.comisiones;
        },
        error: (e) => {
          this.errorDialog(e.error.message.description);
        }
      })

    this.backofficeService.getComite()
      .subscribe({
        next: (res) => {
          this.comiteList = res.result.comites;
        },
        error: (e) => {
          this.errorDialog(e.error.message.description);
        }
      })
  }

  loadDatabyID() {
    this.backofficeService.getUsersbyID(this.accessToken, this.userID)
      .subscribe({
        next: async (res) => {
          this.userData = res.result;
          this.name = res.result.firstName
          this.secondName = res.result.secondName;
          this.lastName = res.result.firstLastname;
          this.secondLastname = res.result.secondLastname;
          this.permissions = res.result.permissions;
          this.genderID = this.userData.gender?.id;
          this.countryID = this.userData.country?.id;
          this.puesto = this.userData.puesto?.id;
          this.userArea = this.userData.area?.id;

          if(res.result?.role) {
            console.log('si existe')
            const role = res.result?.role.slice(5);
            this.userData.roles = role.toLowerCase();
          } else {
            console.log('no existe')
            this.userData.roles = '';
          }

          if (this.userData.birthday) {
            const tempDate = this.userData.birthday.toString().split('-');
            this.model = { day: Number(tempDate[2].substring(0, tempDate[2].indexOf('T'))), month: Number(tempDate[1]), year: Number(tempDate[0]) };
          }

          this.setPermissions()
        },
        error: (e) => this.errorDialog(e.error.message.description)
      })

      this.backofficeService.getCommunicationChannelsByUser(this.accessToken, this.userID)
      .subscribe({
        next: async (res) => {
          this.communicationsChannel = res.result;
        },
        error: (e) => this.errorDialog(e.error.message.description)
      })
  }

  submitUser() {
    if (this.formulario.valid) {

      if (this.userID > 0) {
        // UPDATE
        this.editUser();
      }
      else {
        // NEW
        this.createUserService();
      }
    } else {
      const modalRef = this.modalService.open(ErrorDialogComponent, { centered: true })
      modalRef.componentInstance.errorMessage = '¡Llena los campos obligatorios!';
    }
  }

  createUserService() {
    this.userData.areaId = this.userArea;
    this.userData.puesto_id = this.puesto;
    this.userData.gender_id = this.genderID;
    this.userData.country_id = this.countryID;
    this.userData.firstName = this.name;
    this.userData.secondName = this.secondName
    this.userData.firstLastname = this.lastName;
    this.userData.secondLastname = this.secondLastname;
    this.userData.birthday = new Date(this.model.year, (this.model.month - 1), this.model.day);

    //we need to remove static password when user is created
    this.userData.password = "pass123";
    this.userData.roles = [this.userData.roles]

    console.log('save', this.userData);

    this.successDialog('/admin/users/admin-usuarios', 'SUCCESS');
    // this.backofficeService.createUser(this.userData, this.accessToken)
    //   .subscribe({
    //     next: (res) => {
    //       console.log(res);
    //       // console.log(res.success);
    //       // console.log(res.message.title);
    //       // console.log(res.message.description);
    //       this.open();
    //       this.goToPage('/admin/users/admin-usuarios');
    //     },
    //     error: (e) => {
    //       console.log(e);
    //       this.errorDialog(e.error.message.description);
    //     }
    //   })
  }

  editUser() {
    this.userData.firstName = this.name;
    this.userData.secondName = this.secondName;
    this.userData.firstLastname = this.lastName;
    this.userData.secondLastname = this.secondLastname;

    this.userData.areaId = this.userArea;
    this.userData.puesto_id = this.puesto;
    this.userData.gender_id = this.genderID;
    this.userData.country_id = this.countryID;

    this.userData.role = [this.userData.roles]
    this.userData.roles = [this.userData.roles]
    this.userData.permissions =this.permissions

    this.backofficeService.editUser(this.userData, this.accessToken, this.userID)
      .subscribe({
        next: (res) => {
          console.log(res);
          this.successDialog('/admin/users/admin-usuarios', 'UPDATE')
        },
        error: (e) => {
          this.errorDialog(e.error.message.description);
        }
      })
  }
}

