import { GenericApiResponse } from "src/app/models/generic-api-response.model";
import { SurveyReportModel } from "src/app/models/survey-report-model";

export function getSurveyItemsMock(count: number): GenericApiResponse<SurveyReportModel[]> {
    return {
        success: true,
        message: {
          title: 'Success',
          description: 'Survey items report retrieved successfully',
        },
        result: generateItems(count),
      }
}

function generateItems(count: number): SurveyReportModel[] {
    const items: SurveyReportModel[] = [];
  
    let id = 1;
    for (let i = 0; i < count; i++) {
      const sector = `Sector ${i + 1}`
      const eventName = `Event ${i + 1}`
      const comision = `Comisión ${i + 1}`
      const userName = `Company ${i}`;
      const mainContact = `Contacto ${i + 1}`
      const eventDate = '2022-01-01';
      const question1 = `Question ${i + 1}`
      const question2 = `Question ${i + 1}`
      const question3 = `Question ${i + 1}`

  
  
      const itemReport: SurveyReportModel = {
        id,
        sector,
        comision,
        userName,
        eventName,
        eventDate,
        question1, 
        question2,
        question3,
        
      };
      
      items.push(itemReport);
      id++;
    }
  
    return items;
  }

  
  