import { Component, OnInit } from '@angular/core';
import { KeyValue } from '@angular/common';
import { listas } from 'src/app/models/back-service.model';
import {
  NgbDateStruct,
  NgbCalendar,
  NgbDatepicker,
  NgbDatepickerConfig,
} from '@ng-bootstrap/ng-bootstrap';
import { PromotionsService } from 'src/app/services/promotions.service';
import { UtilsService } from 'src/app/services/utils.service';
import { EventService } from 'src/app/services/event.service';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { Promotion } from 'src/app/models/promotions-models';
import { EventItem } from 'src/app/models/event-report-model';
import { CertificatesService } from 'src/app/services/certificates-service.service';
import { SearchReportParamsModel } from 'src/app/models/search-report-params-model';
import { getAssociatedConstanceMock } from './mocks/associated-constance-mock';
import { AssociatedConstanceItemReport } from 'src/app/models/associated-constance-model';
import { PointsCertificateModel } from 'src/app/models/points-certificate-model';
import { getPointsCertificateMock } from './mocks/points-certificate-mock';
import { getSurveyItemsMock } from './mocks/survey-event-mock';
import { SurveyReportModel } from 'src/app/models/survey-report-model';

@Component({
  selector: 'app-reporteria',
  templateUrl: './reporteria.component.html',
  styleUrls: ['./reporteria.component.css'],
})
export class ReporteriaComponent implements OnInit {
  dropdownFilterItems: string[] = [];
  staticsList: Array<string> = [
    'Seleccione',
    'Promoción',
    'Promoción en especifico',
    'Eventos',
    'Constancia de asociado',
    'Certificado de puntos',
    'Encuestas eventos',
  ];
  statusList: Array<string> = ['Activo', 'Inactivo'];
  model!: NgbDateStruct;
  selectedStatic: string | undefined;

  statusSelected: string = 'active';

  selectedFilter: string = '';

  categoriesHeading: string[] = [];
  list: {}[] = [];
  filtro = '';

  start_date: NgbDateStruct | undefined;
  end_date: NgbDateStruct | undefined;

  startDateString: string = '';
  endDateString: string = '';

  totalPages = 0;
  pageSize = 10;
  pageNum = 0;

  searchParams: SearchReportParamsModel | undefined;

  constructor(
    private promotionsService: PromotionsService,
    private utilsService: UtilsService,
    private eventService: EventService,
    private associatedConstanceService: CertificatesService
  ) {}

  ngOnInit(): void {
    this.selectedStatic = this.staticsList[0];
    this.statusSelected = this.statusList[0];
  }

  originalOrder = (
    a: KeyValue<string, string>,
    b: KeyValue<string, string>
  ): number => {
    return 0;
  };

  getDates() {
    this.startDateString = this.start_date
      ? `${this.start_date.year}-${this.start_date.month
          .toString()
          .padStart(2, '0')}-${this.start_date.day.toString().padStart(2, '0')}`
      : '';
    this.endDateString = this.end_date
      ? `${this.end_date.year}-${this.end_date.month
          .toString()
          .padStart(2, '0')}-${this.end_date.day.toString().padStart(2, '0')}`
      : '';
  }

  getStatus() {
    switch (this.statusSelected) {
      case 'Activo':
        return 'active';
      case 'Inactivo':
        return 'inactive';
      default:
        return 'active';
        break;
    }
  }

  searchData() {
    this.getDates();
    switch (this.selectedStatic) {
      case 'Promoción':
        {
          this.loadPromotionsData(false);
          this.selectedFilter = 'Nombre de promoción';
        }
        break;
      case 'Eventos':
        {
          this.getAllEvents();
          this.selectedFilter = 'Nombre del evento';
        }
        break;
      case 'Promoción en especifico':
        {
          this.loadPromotionsData(true);
          this.selectedFilter = 'Nombre de promoción';
        }
        break;
      case 'Constancia de asociado':
        {
          this.getAssociatedConstanceItems();
          this.selectedFilter = 'Nombre de la empresa';
        }
        break;
      case 'Certificado de puntos':
        {
          this.getPointsCertificateItems();
          this.selectedFilter = 'Nombre de la empresa';
        }
        break;
      case 'Encuestas eventos':
        {
          this.getSurveyReportItems();
          this.selectedFilter = 'Nombre del evento';
        }
        break;

      default:
        this.list = [];
        break;
    }
  }

  async loadPromotionsData(especifico: boolean) {
    try {
      const response = await this.promotionsService.getAllPromotions(
        this.getStatus(),
        '',
        0,
        100,
        this.startDateString,
        this.endDateString
      );

      if (response?.success === true) {
        this.setDropDownFilter(response, 'promotion');

        if (especifico) {
          this.list = response.result.map((e: Promotion) => {
            return {
              'Nombre de promoción': e.name,
              Código: e.id,
              'Visitas a mi promoción': '*12',
              'Canjes de promoción': e.redeemCount,
              Correo: '*micorreo@gmail.com',
              Teléfono: e.phone,
              Compartidos: '*12',
              Interacciones: '*12',
              'Nuevas vistas': '*12',
            };
          });
          this.categoriesHeading = Object.keys(this.list[0]);
        } else {
          this.list = response.result.map((e: Promotion) => {
            return {
              'Nombre de promoción': e.name,
              Código: e.id,
              'Canjes de promoción': e.redeemCount,
              Categoría: e.categoryName,
            };
          });
          this.categoriesHeading = Object.keys(this.list[0]);
        }
      } else {
        console.log('error 1', response);
        this.utilsService.dialog(
          'ERROR',
          'Error',
          '¡Error al consultar las promociones'
        );
      }
    } catch (error) {
      console.log(error);
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'No se encontraron promociones '
      );
    }
  }

  getAllEvents() {
    this.eventService.getEvents(this.pageNum, this.pageSize).subscribe({
      next: async (res) => {
        this.setDropDownFilter(res, 'event');

        this.list.pop();
        this.list = res.result.map((e: EventItem) => {
          return {
            Sector: 'Servicios',
            Comisión: e.eventType.name,
            'Área organizacional': '*área organizacional',
            Organizador: '*organizador',
            'Nombre del evento': e?.title,
            Costo: e.costType.name,
            'Fecha de creación del evento': this.utilsService.date_pipe(
              e?.eventCreation,
              'dd-MM-yyy'
            ),
            'Fecha de inicio': this.utilsService.date_pipe(
              e?.fechaInicio,
              'dd-MM-yyy'
            ),
            'Fecha de fin': this.utilsService.date_pipe(
              e?.fechaFinal,
              'dd-MM-yyy'
            ),
            'Menos de 15 días de publicado': e.status,
            'Meta de asociados esperados': '*30',
            'Meta de asociados no esperados': '*50',
            'Meta total esperado': '*80',
            'Real de asistentes Asociados': '*18',
            'Real total registrado': '*40',
            Eficiencia: '*80%',
            'Empresas Asociadas': '*14',
            'Empresas No Asociadas': '*10',
            'Total empresas': '*24',
          };
        });
        this.totalPages = Math.ceil(res.count / this.pageSize);
        this.categoriesHeading = Object.keys(this.list[0]);
      },
      error: (e) =>
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        ),
    });
  }

  async getAssociatedConstanceItems() {
    try {
      /*  this.searchParams!.name =''
        this.searchParams!.page = '0'
        this.searchParams!.size =  '100'
        this.searchParams!.startDate = '01-01-2023'
        this.searchParams!.name = '31-12-2023'

        const response = await this.associatedConstanceService.getAllAssociatedConstanceItems(this.searchParams!);
 */

      const response = getAssociatedConstanceMock(10);

      if (response?.success === true) {
        this.setDropDownFilter(response, 'associated');

        this.list = response.result.map((e: AssociatedConstanceItemReport) => {
          return {
            Sector: e.sector,
            Comisión: e.comision,
            'Nombre de la empresa': e.companyName,
            'Nombre o contacto principal asignado': e.mainContact,
            Nit: e.Nit,
            'Número de certificado': e.certificateNumber,
            'Fecha de emisión': e.creationDate,
          };
        });
        this.categoriesHeading = Object.keys(this.list[0]);
      } else {
        console.log('error 1', response);
        this.utilsService.dialog(
          'ERROR',
          'Error',
          '¡Error al consultar las promociones'
        );
      }
    } catch (error) {
      console.log(error);
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'No se encontraron promociones '
      );
    }
  }
  async getPointsCertificateItems() {
    try {
      /* this.searchParams!.name =''
        this.searchParams!.page = '0'
        this.searchParams!.size =  '100'
        this.searchParams!.startDate = '01-01-2023'
        this.searchParams!.name = '31-12-2023'

        const response = await this.associatedConstanceService.getAllPointsCertificatesItems(this.searchParams!); */

      const response = getPointsCertificateMock(10);

      if (response?.success === true) {
        this.setDropDownFilter(response, 'points');

        this.list = response.result.map((e: PointsCertificateModel) => {
          return {
            Sector: e.sector,
            Comisión: e.comision,
            'Nombre de la empresa': e.companyName,
            'Nombre o contacto principal asignado': e.mainContact,
            Nit: e.Nit,
            'Número de certificado': e.certificateNumber,
            'Puntos acumulados': e.accumulatePoints,
            'Total en quetzales': e.quetzalsTotal,
            'Fecha de emisión': e.creationDate,
          };
        });
        this.categoriesHeading = Object.keys(this.list[0]);
      } else {
        console.log('error 1', response);
        this.utilsService.dialog(
          'ERROR',
          'Error',
          '¡Error al consultar las promociones'
        );
      }
    } catch (error) {
      console.log(error);
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'No se encontraron promociones '
      );
    }
  }

  async getSurveyReportItems() {
    try {
      /* this.searchParams!.name =''
      this.searchParams!.page = '0'
      this.searchParams!.size =  '100'
      this.searchParams!.startDate = '01-01-2023'
      this.searchParams!.name = '31-12-2023'

      const response = await this.associatedConstanceService.getAllPointsCertificatesItems(this.searchParams!); */

      const response = getSurveyItemsMock(10);

      if (response?.success === true) {
        this.setDropDownFilter(response, 'survey');

        this.list = response.result.map((e: SurveyReportModel) => {
          return {
            'Nombre del evento': e.eventName,
            'Fecha del evento': e.eventDate,
            Sector: e.sector,
            Comisión: e.comision,
            'Nombre del usuario': e.userName,
            'Pregunta 1': e.question1,
            'Pregunta 2': e.question2,
            'Pregunta 3': e.question3,
          };
        });
        this.categoriesHeading = Object.keys(this.list[0]);
      } else {
        console.log('error 1', response);
        this.utilsService.dialog(
          'ERROR',
          'Error',
          '¡Error al consultar las promociones'
        );
      }
    } catch (error) {
      console.log(error);
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'No se encontraron promociones '
      );
    }
  }

  setDropDownFilter(res: any, type: string) {
    this.dropdownFilterItems = [];

    this.dropdownFilterItems.push('');
    switch (type) {
      case 'event':
        {
          res.result.map((e: EventItem) => {
            this.dropdownFilterItems.push(e.title);
          });

          this.filtro = this.dropdownFilterItems[0];
        }
        break;
      case 'promotion':
        {
          res.result.map((e: Promotion) => {
            this.dropdownFilterItems.push(e.name);
          });

          this.filtro = this.dropdownFilterItems[0];
        }
        break;
      case 'associated':
        {
          res.result.map((e: AssociatedConstanceItemReport) => {
            this.dropdownFilterItems.push(e.companyName!);
          });

          this.filtro = this.dropdownFilterItems[0];
        }
        break;
      case 'points':
        {
          res.result.map((e: PointsCertificateModel) => {
            this.dropdownFilterItems.push(e.companyName!);
          });

          this.filtro = this.dropdownFilterItems[0];
        }
        break;
      case 'survey':
        {
          res.result.map((e: SurveyReportModel) => {
            this.dropdownFilterItems.push(e.eventName!);
          });

          this.filtro = this.dropdownFilterItems[0];
        }
        break;

      default:
        break;
    }
  }

  onLeft() {
    if (this.pageNum + 1 != 1) {
      this.pageNum--;
      this.list.length = 0;
      this.searchData();
    }
  }

  onRight() {
    if (this.pageNum + 1 != this.totalPages) {
      this.pageNum++;
      this.list.length = 0;
      this.searchData();
    }
  }

  async downloadToExcel() {
    const reportName =
      `Reporte - ${this.selectedStatic} ` +
      this.utilsService.getDate('dd-MM-yyy');
    this.utilsService.downloadToExcel(this.list, 'Reportes', reportName);
  }
}
