import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BackService } from '../models/back-service.model';
import { PasswordRecoveryCodeComponent } from '../password-recovery-code/password-recovery-code.component';
import { BackofficeService } from '../services/backoffice.service';
import { ErrorDialogComponent } from 'src/app/Extra/error-dialog/error-dialog.component';
import {NgbModal} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-new-password',
  templateUrl: './new-password.component.html',
  styleUrls: ['./new-password.component.css']
})


export class NewPasswordComponent implements OnInit {
  reset : any;
  show: boolean = false;
  password: string = '';
  confirmpassword: string = '';
  loginData: BackService = {
    newPassword: ''
  };
  ngOnInit() {
    if(sessionStorage.getItem('tempToken'))
    {
      this.reset = sessionStorage.getItem('tempToken')!== null ? sessionStorage.getItem('tempToken') : "";
    }
    else{
      const modalRef = this.modalService.open(ErrorDialogComponent, {centered: true})
      modalRef.componentInstance.errorMessage = "No tienes acceso...";
      this.goToPage('/login');
    }
  }

  constructor(private router: Router, private backofficeService: BackofficeService, private modalService: NgbModal) { }

  goToPage(pageName:string){
    this.router.navigate([`${pageName}`]);
  }

  changePassword()
  {
    switch(true){
      case this.password !== this.confirmpassword:
        {
            const modalRef = this.modalService.open(ErrorDialogComponent, {centered: true})
            modalRef.componentInstance.errorMessage = "Las claves ingresadas son distintas.";
        }
        break;
      case (!this.passwordValidator()):
        {
          const modalRef = this.modalService.open(ErrorDialogComponent, {centered: true})
          modalRef.componentInstance.errorMessage = "La contraseña debe tener como mínimo 6 caracteres siendo al menos 1 mayúscula.";
        }
        break;
      case (this.passwordValidator() && this.password === this.confirmpassword):
        this.callService();
        break;
    }

  }

  passwordValidator() {
        let plus6 = this.password.length > 5;
        let hasUpper = /[A-Z]/.test(this.password);
        
        if( hasUpper && plus6)
        {
          return true;
        }

        return false;
  }

  callService()
  {
    this.loginData = {
      newPassword: this.password
    }
    console.log(this.reset);

    this.backofficeService.newpassword(this.loginData, this.reset)
        .subscribe({
          next: (res) => {
            sessionStorage.removeItem('tempToken');
            sessionStorage.removeItem('recoverEmail');
            this.goToPage('/login');
          },
          error: (e) => {
            const modalRef = this.modalService.open(ErrorDialogComponent, {centered: true})
            modalRef.componentInstance.errorMessage = "No se pudo actualizar la contraseña. Por favor vuelve a intentar más tarde.";
          } 
        })
  }

  passwordVisibility() {
    this.show = !this.show;
  }

}
