import { NgForm } from '@angular/forms';
import { Component, OnInit, ViewChild } from '@angular/core';
import { UtilsService } from 'src/app/services/utils.service';
import { SponsorService } from 'src/app/services/sponsor.service';
import { BackServiceSponsors } from 'src/app/models/back-service.model';

@Component({
  selector: 'app-edit-patrocinadores',
  templateUrl: './edit-patrocinadores.component.html',
  styleUrls: ['./edit-patrocinadores.component.css']
})
export class EditPatrocinadoresComponent implements OnInit {
  @ViewChild('AGForm') formulario!: NgForm;

  public eventID: number = 0;
  public sponsorID: number = 0;
  public loading: boolean = false;
  public disabled: boolean = false;
  public sponsorAux: any = {} as BackServiceSponsors;
  public sponsor: BackServiceSponsors = {} as BackServiceSponsors;

  constructor(
    private utilsService: UtilsService,
    private sponsorService: SponsorService,
  ) {
    this.eventID = Number(sessionStorage.getItem('EventID'))
    this.sponsor.logotipo = { data: '', extension: '', name: '', url: '' };
  }

  async ngOnInit(): Promise<void> {
    this.sponsorID = Number(this.utilsService.getParam('ID'));
    const token: string | undefined = sessionStorage.getItem('Token')?.toString();

    if (token) {
      if (this.eventID > 0) {
        if (this.sponsorID === 0) {
          this.sponsor.id = 0;
          this.sponsor.eventId = this.eventID;
        } else {
          await this.getOneSponsor(this.sponsorID);
        }
      } else {
        this.utilsService.dialog('ERROR', 'Alerta', '¡No tienes seleccionado un evento!');
        this.goToPage('/admin/events/calendario-eventos');
      }
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', '¡No tienes acceso!');
      this.goToPage('/login');
    }
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName);
  }

  async addFile(event: any) {
    const file = event.target.files[0];
    const extension = file.type.split('/')
    const base64 = await this.utilsService.convertBase64(event.target.files.item(0));
    // const imageType = file.type.substring(file.type.indexOf('/') + 1) // Method #2
    // const preview = await this.utilsService.previewImage(event.target.files);  

    if (typeof (base64) === 'string') {
      this.sponsor.logotipo = { extension: extension[1], name: file.name, data: base64, url: '' }
    }
  }

  getOneSponsor(sponsor_id: number) {
    this.loading = true;
    return this.sponsorService.getOneSponsor(sponsor_id).then(
      (response) => {
        this.sponsor.eventId = this.eventID;
        this.sponsor.id = response.result.sponsorId;
        this.sponsor.website = response.result?.website;
        this.sponsor.descripcion = response.result?.description;
        this.sponsor.nombreUsuario = response.result?.sponsorUser;
        this.sponsor.nombrePatrocinador = response.result?.sponsorName;
        this.sponsor.email = response.result?.itemsDescription[1]?.title;
        this.sponsor.telefono = response.result?.itemsDescription[0]?.title;    
        this.sponsor.ubicacion = response.result?.itemsDescription[2]?.title;

        this.sponsor.twitter = response.result?.socialNetworks[2]?.title;
        this.sponsor.facebook = response.result?.socialNetworks[1]?.title;
        this.sponsor.linkedIn = response.result?.socialNetworks[3]?.title;
        this.sponsor.instagram = response.result?.socialNetworks[0]?.title;



        if (typeof response.result?.imageHeader === 'string') {
          const logotipo = response.result.imageHeader.split('/');
          this.sponsor.logotipo = { data: '', extension: '', name: logotipo[6], url: response.result?.imageHeader };
        } else {
          this.sponsor.logotipo = { data: '', extension: '', name: '', url: '' };
        }

        // console.log(response.result)
        // console.log(this.sponsor)
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Error al cargar patrocinador!');
        this.goToPage('/admin/events/admin-patrocinadores');
      }).finally(() => {
        this.loading = false
      })
  }


  processData() {
    if (this.formulario.valid) {
      this.sponsorAux = {} as BackServiceSponsors;
      if (this.sponsor.logotipo.data === '') {
        const { logotipo, ...data } = this.sponsor;
        this.sponsorAux = data;
      } else {
        this.sponsorAux = this.sponsor;
      }

      if (this.sponsorID > 0) {
        this.updateSponsor(this.sponsorAux);
      }
      else {
        this.createSponsor(this.sponsorAux);
      }
    } else {
      this.utilsService.dialog('ERROR', 'Error', 'Por favor llena los campos obligatorios.');
    }
  }

  updateSponsor(sponsorData: BackServiceSponsors) {
    console.log('UPDATE ', sponsorData);
    this.loading = true;
    this.disabled = true;
    
    this.sponsorService.updateSponsorByEvent(this.sponsorID, sponsorData).then(
      (response) => {
        console.log(response);
        this.utilsService.dialog('SUCCESS', 'Exito', '¡Patrocinador actualizado!');
        this.goToPage('/admin/events/admin-patrocinadores');
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Al actualizar patrocinador!');
      }).finally(() => {
        this.loading = false
        this.disabled = false;
      });
  }

  createSponsor(sponsorData: BackServiceSponsors) {
    console.log('CREATE ', sponsorData)

    this.loading = true;
    this.disabled = true;

    this.sponsorService.createSponsorToEvent(sponsorData).then(
      (response) => {
        // console.log(response);
        this.utilsService.dialog('SUCCESS', 'Exito', '¡Patrocinador registrado!');        
        this.goToPage('/admin/events/admin-patrocinadores');
      },
      error => {
        // console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Al registrar patrocinador!');
      }).finally(() => {
        this.loading = false
        this.disabled = false;
      });
  }
}
