import { Component, OnInit } from '@angular/core';
import { KeyValue } from '@angular/common';
import { BackServiceUsers } from 'src/app/models/back-service.model';
import { UtilsService } from 'src/app/services/utils.service';
import { ExhibitorService } from 'src/app/services/exhibitors.service';

@Component({
  selector: 'app-admin-expositores',
  templateUrl: './admin-expositores.component.html',
  styleUrls: ['./admin-expositores.component.css']
})
export class AdminExpositoresComponent implements OnInit {
  // public dates: any[] = []
  // public modality: any[] = [];
  public filtro: string = '';
  public pageSize: number = 20;
  public pageNumber: number = 0;
  public loading: boolean = false;
  public isShowDiv: boolean = true;
  public categoriesHeading: string[] = [];
  public list: any[] = [{ 'Nombre expositores': '', 'Nombre Empresa': '', 'Correo electrónico': '', 'Estatus': '' }];

  // usersList: any;
  // list: {}[] = [    
  //   { "Nombre expositor": "Expositor A", "Nombre Empresa": "Empresa A", "Correo electrónico": "info@gmail.com", "Estatus": "Registrado" },
  //   { "Nombre expositor": "Expositor B", "Nombre Empresa": "Empresa B", "Correo electrónico": "info@gmail.com", "Estatus": "Sin Registro" },
  //   { "Nombre expositor": "Expositor C", "Nombre Empresa": "Empresa C", "Correo electrónico": "info@gmail.com", "Estatus": "Registrado" }
  // ];

  constructor(
    private utilsService: UtilsService,
    private exhibitorService: ExhibitorService
  ) {
    this.categoriesHeading = Object.keys(this.list[0]);
  }

  ngOnInit(): void {
    this.isShowDiv = true;
    const token: string | undefined = sessionStorage.getItem('Token')?.toString();
    const eventID: number | undefined = Number(sessionStorage.getItem('EventID'));

    if (token && eventID > 0) {
      this.loadDatabyEvent(eventID);
    }
    else if (token && eventID === 0) {
      this.utilsService.dialog('ERROR', 'Error', 'No tienes seleccionado un evento');
      this.utilsService.goToPage('/admin/events/calendario-eventos');
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', 'No tienes seleccionado un evento');
      this.utilsService.goToPage('/login');
    }
  }

  originalOrder = (a: KeyValue<string, string>, b: KeyValue<string, string>): number => {
    return 0;
  }

  goToPage(pageName: string, params?: {}) {
    this.utilsService.goToPage(pageName, params);
  }

  showActions(event: any) {
    this.isShowDiv = true;
    const element = document.getElementById('item' + event.id);
    const listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if (('item' + event.id) !== listItes?.id || !listItes.id) {
      element?.classList.add('actions-disble');
    }
  }

  loadDatabyEvent(event_id: number) {
    this.loading = true;
    return this.exhibitorService.getExhibitorsByEvent(this.pageNumber, this.pageSize, event_id).then(
      response => {
        console.log(response)
        if (response.count > 0) {
          this.list = response.result.exhibitors.map((e: any) => {
            return {
              id: e.id,
              'Nombre expositores': e?.nombreExpositor,
              'Nombre Empresa': e?.nombreEmpresa,
              'Correo electrónico': e?.email,
              'Estatus': e?.registro ? 'Registrado' : 'Sin Registro'
            }
          });
          // sessionStorage.setItem('Participant', JSON.stringify(response.result.rows));
        }
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Intente mas tarde!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  updateRegister($event: any, index: number) {
    this.loading = true;
    const participantID = this.list[index].id;
    const register = $event.target.value === 'Registrado' ? 1 : 0;//true : false;

    // participantID, { registro: register }

    // const json = {
    //   "nombreExhibitors": "Empresa 1 edit",
    //   "nombreEmpresa": "Empresa 1 edit",
    //   "email": "empresa_edit@gmail.com",
    //   "status": register,
    //   "eventId": 114
    // }
    // this.exhibitorService.updateExhibitorsByEvent(participantID, json).then(
    this.exhibitorService.updateExhibitorsByEvent(participantID, { status: register }).then(
      response => {
        console.log('Exito')
        this.utilsService.dialog('SUCCESS', 'Exito', 'Expositor actualizado.');
      },
      error => {
        console.log(error)
        this.utilsService.dialog('ERROR', 'Error', '¡Intente mas tarde!');

        if ($event.target.value == 'Sin Registro') {
          $event.target.value = 'Registrado'
          this.list[index].Registro = 'Registrado'
        } else {
          $event.target.value = 'Sin Registro'
          this.list[index].Registro = 'Sin Registro'
        }
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  deleteExhibitor(exhibitor_id: number) {
    this.loading = true;
    const eventID: number | undefined = Number(sessionStorage.getItem('EventID'));

    this.exhibitorService.deleteExhibitors(exhibitor_id).then(
      response => {
        this.utilsService.dialog('SUCCESS', 'Exito', 'Expositor eliminado');
        this.loadDatabyEvent(eventID);
      },
      error => {
        console.log(error)
      }
    ).finally(() => {
      this.loading = false;
    });
  }
}
