import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
    providedIn: 'root'
})

export class ScheduleService {
    private headers: {} = {};
    private url: string = environment.url;
    private accessToken: string | undefined;

    constructor(private http: HttpClient) {
        this.accessToken = sessionStorage.getItem('Token')?.toString();

        this.headers = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'accept': 'application/json',
                'x-access-token': this.accessToken!
            })
        };
    }

    // ACTIVITY    
    getActivityByEvent(event_id: number): Promise<any> {
        return this.http.get(`${this.url}eventos/activities/eventBackOffice/${event_id}`, this.headers).toPromise();
    }

    // getOneActivity(activity_id: number): Promise<any> {        
    //     return this.http.get(`${this.url}eventos/activities/${activity_id}`, this.headers).toPromise();
    // }

    // createActivity(data: any): Promise<any> {
    //     return this.http.post(`${this.url}eventos/activities/`, data, this.headers).toPromise();
    //     // return this.http.post(`${this.url}eventos/activities/eventBackOffice/`, data, this.headers).toPromise();
    // }

    // editActivity(activity_id: number, data: any): Promise<any> {
    //     return this.http.put(`${this.url}eventos/activities/${activity_id}`, data, this.headers).toPromise();
    //     // return this.http.put(`${this.url}eventos/activities/eventBackOffice/${activity_id}`, data, this.headers).toPromise();
    // }

    // deletedActivity(activity_id: any): Promise<any> {
    //     return this.http.delete(`${this.url}eventos/activities/${activity_id}`, this.headers).toPromise();
    // }

    // SCHEDULE
    getScheduleByEvent(event_id: number): Promise<any> {                                      
        return this.http.get(`${this.url}eventos/schedules/event/${event_id}`, this.headers).toPromise();
    }

    getOneSchedule(schedule_id: number): Promise<any> {     
        return this.http.get(`${this.url}eventos/schedules/${schedule_id}`, this.headers).toPromise();
    }

    createSchedule(data: any): Promise<any> {                                           
        return this.http.post(`${this.url}eventos/schedules/`, data, this.headers).toPromise();
    }

    editSchedules(schedule_id: number, data: any): Promise<any> {
        return this.http.put(`${this.url}eventos/schedules/${schedule_id}`, data, this.headers).toPromise();
    }

    deletedSchedule(schedule_id: any): Promise<any> {
        return this.http.delete(`${this.url}eventos/schedules/${schedule_id}`, this.headers).toPromise();
    }
}
