import { KeyValue } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { BackServiceUsers, BackServiceParticipants, userbyEvent } from 'src/app/models/back-service.model';
import { BackofficeService } from 'src/app/services/backoffice.service';
import { ParticipantService } from 'src/app/services/participant.service';
import { SpeakerService } from 'src/app/services/speaker.service';
import { UserService } from 'src/app/services/user.service';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
  selector: 'app-user-guide',
  templateUrl: './user-guide.component.html',
  styleUrls: ['./user-guide.component.css']
})

export class UserGuideComponent implements OnInit {
  public pageNum = 0;
  public pageSize = 15;
  public totalPages = 0;
  public usersList: any;
  public filtro: string = '';
  public typeGuide: string = '';
  public loading: boolean = false;
  public eventID: number | undefined = 0;
  public categoriesHeading: string[] = [];
  public usersData = new BackServiceUsers();
  public userbyEventData = new userbyEvent();
  public participantsData = new BackServiceParticipants();

  public list: {}[] = [
    { "Nombre del usuario": "", "Nombre Empresa": "", "Correo electrónico": "" }
  ];

  constructor(
    private userService: UserService,
    private utilsService: UtilsService,
    private speakerService: SpeakerService,
    private participantService: ParticipantService
  ) {
    this.categoriesHeading = Object.keys(this.list[0]);
  }

  originalOrder = (a: KeyValue<string, string>, b: KeyValue<string, string>): number => {
    return 0;
  }

  ngOnInit(): void {
    this.typeGuide = this.utilsService.getParam('type');
    this.eventID = Number(sessionStorage.getItem('EventID'));
    const token: string | undefined = sessionStorage.getItem('Token')?.toString();

    if (token && this.eventID > 0) {
      switch (this.typeGuide) {
        case "participantes":
          this.loadUser();
          break;
        case "conferencistas":
          this.loadUser();
          break;
        case "expositores":
          this.loadUser();
          break;
        case "patrocinadores":
          this.loadUser();
          break;
        case "empresas":
          break;
        default:
          this.utilsService.dialog('ERROR', 'Error', 'Error al cargar modulo');
          this.utilsService.goToPage('/admin/inicio');
          break;
      }
    }
    else if (token && this.eventID === 0) {
      this.utilsService.dialog('ERROR', 'Error', 'No tienes seleccionado un evento');
      this.utilsService.goToPage('/admin/events/calendario-eventos');
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', 'No tienes seleccionado un evento');
      this.utilsService.goToPage('/login');
    }
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName + this.typeGuide);
  }

  onLeft() {
    if ((this.pageNum + 1) != 1) {
      this.pageNum--;
      this.list.length = 0;
      this.loadUser();
    }
  }

  onRight() {
    if ((this.pageNum + 1) != this.totalPages) {
      this.pageNum++;
      this.list.length = 0;
      this.loadUser();
    }
  }

  search() {
    console.log(this.filtro)
  }

  loadUser() {
    this.loading = true;
    return this.userService.getAllUsers(this.pageNum, this.pageSize).then(
      response => {
        console.log(response.result);

        if (this.typeGuide === 'participantes') {
          this.list = response.result.map((e: any) => {
            return {
              id: e.id,
              'Nombre usuario': `${e.firstName} ${e.secondName} ${e.firstLastname} ${e.secondLastname}`,
              'Nombre Empresa': e.company?.nombreComercial,
              'Correo electrónico': e.email
            }
          });
        } else if (this.typeGuide === 'conferencistas') { //expositores
          this.list = response.result.map((e: any) => {
            return {
              id: e.id,
              'Nombre usuario': `${e.firstName} ${e.secondName} ${e.firstLastname} ${e.secondLastname}`,
              'Nombre Empresa': e.company?.nombreComercial,
              'Correo electrónico': e.email
            }
          });
        } else if (this.typeGuide === 'expositores') {
          this.list = response.result.map((e: any) => {
            return {
              id: e.id,
              'Nombre usuario': `${e.firstName} ${e.secondName} ${e.firstLastname} ${e.secondLastname}`,
              'Nombre Empresa': e.company?.nombreComercial,
              'Correo electrónico': e.email
            }
          });
        } else if (this.typeGuide === 'patrocinadores') {
          this.list = response.result.map((e: any) => {
            return {
              id: e.id,
              'Nombre usuario': `${e.firstName} ${e.secondName} ${e.firstLastname} ${e.secondLastname}`,
              'Nombre Empresa': e.company?.nombreComercial,
              'Correo electrónico': e.email
            }
          });
        }

        this.totalPages = Math.ceil(response.count / this.pageSize);
        // sessionStorage.setItem('Participant', JSON.stringify(response.result.rows));
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Intente mas tarde!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  addParticipant(user_id: number) {
    this.loading = true;
    this.userbyEventData.userId = user_id;
    this.userbyEventData.eventId = this.eventID;
    
    console.log(this.userbyEventData);

    return this.participantService.addParticipantToEvent(this.userbyEventData).then(
      async response => {
        console.log(response)
        this.utilsService.dialog('Success', 'Exito', '¡Usuario agregado como participante!');
        await this.loadUser();
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Usuario no agregado como participante!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  addSpeaker(user_id: number) {
    this.loading = true;
    this.userbyEventData.userId = user_id;
    this.userbyEventData.eventId = this.eventID;

    console.log(this.userbyEventData);

    return this.speakerService.addSpeakerToEvent(this.userbyEventData).then(
      async response => {
        console.log(response)
        await this.loadUser();
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Usuario no agregado como conferencista!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  addCompany(user_id: number) {
    this.loading = true;
    this.userbyEventData.userId = user_id;
    this.userbyEventData.eventId = this.eventID;

    console.log(this.userbyEventData);

    // return this.speakerService.addSpeakerToEvent(this.userbyEventData).then(
    //   async response => {
    //     console.log(response)
    //     await this.loadUser();
    //   },
    //   error => {
    //     console.log(error);
    //     this.utilsService.dialog('ERROR', 'Error', '¡Usuario no agregado a la empresa!');
    //   }
    // ).finally(() => {
    //   this.loading = false;
    // });
  }
}
