import { AssociatedConstanceItemReport } from "src/app/models/associated-constance-model";
import { GenericApiResponse } from "src/app/models/generic-api-response.model";
import { Promotion, PromotionResponse } from "src/app/models/promotions-models";

export function getAssociatedConstanceMock(count: number): GenericApiResponse<AssociatedConstanceItemReport[]> {
    return {
        success: true,
        message: {
          title: 'Success',
          description: 'Promotions retrieved successfully',
        },
        result: generateConstancesItems(count),
      }
}

function generateConstancesItems(count: number): AssociatedConstanceItemReport[] {
    const items: AssociatedConstanceItemReport[] = [];
  
    let id = 1;
    for (let i = 0; i < count; i++) {
      const sector = `Sector ${i + 1}`
      const comision = `Comisión ${i + 1}`
      const companyName = `Company ${i}`;
      const Nit = `Category ${Math.floor(Math.random() * 5) + 1} - A`
      const certificateNumber =  `Category ${Math.floor(Math.random() * 5) + 1} - B`
      const phone = `555-${Math.floor(Math.random() * 900) + 100}-XXXX`;
      const image = `https://picsum.photos/id/${Math.floor(Math.random() * 1000)}/200`;
      const mainContact = `Contacto ${i + 1}`
      const creationDate = '2022-01-01';
  
  
      const itemReport: AssociatedConstanceItemReport = {
        id,
        sector,
        comision,
        companyName,
        Nit,
        certificateNumber,
        creationDate,
        mainContact
      };
      
      items.push(itemReport);
      id++;
    }
  
    return items;
  }

  
  