import { Component, OnInit } from '@angular/core';
import { KeyValue } from '@angular/common';
import { Router } from '@angular/router';
import { BackServiceUsers } from 'src/app/models/back-service.model';
import { BackofficeService } from 'src/app/services/backoffice.service';
import { UtilsService } from 'src/app/services/utils.service';
import { SponsorService } from 'src/app/services/sponsor.service';

@Component({
  selector: 'app-admin-patrocinadores',
  templateUrl: './admin-patrocinadores.component.html',
  styleUrls: ['./admin-patrocinadores.component.css']
})
export class AdminPatrocinadoresComponent implements OnInit {
  // public active: number = 1;
  public filtro: string = '';
  public eventID: number = 0;
  public loading: boolean = false;
  public isShowDiv: boolean = true;
  public categoriesHeading: string[] = [];
  public listHeading: any[] = [{ 'Nombre del patrocinador': '', 'Nombre del usuario': '', 'Correo electrónico': '', 'Sitio web': '', 'Asociado': '' }];
  public list: any[] = [];

  constructor(
    private utilsService: UtilsService,
    private sponsorService: SponsorService
  ) { this.categoriesHeading = Object.keys(this.listHeading[0]); }

  ngOnInit(): void {
    this.isShowDiv = true;

    this.isShowDiv = true;
    this.eventID = Number(sessionStorage.getItem('EventID'));
    const token: string | undefined = sessionStorage.getItem('Token')?.toString();

    if (token && this.eventID > 0) {
      this.loadDatabyEvent(this.eventID);
    }
    else if (token && this.eventID === 0) {
      this.utilsService.dialog('ERROR', 'Error', 'No tienes seleccionado un evento');
      this.utilsService.goToPage('/admin/events/calendario-eventos');
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', '¡Debes iniciar sesión!');
      this.utilsService.goToPage('/login');
    }
  }

  originalOrder = (a: KeyValue<string, string>, b: KeyValue<string, string>): number => {
    return 0;
  }

  goToPage(pageName: string, params?: {}) {
    this.utilsService.goToPage(pageName, params)
  }

  showActions(event: any) {
    this.isShowDiv = true;
    const element = document.getElementById('item' + event);
    const listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if (('item' + event) !== listItes?.id || !listItes.id) {
      element?.classList.add('actions-disble');
    }
  }

  loadDatabyEvent(event_id: number) {
    this.loading = true;
    return this.sponsorService.getSponsorByEvent(event_id).then(
      response => {
        // console.log(response.result);
        if (response.result.length > 0) {
        this.list = [];
        this.list = response.result.map((e: any) => {
          return {
            id: e.id,
            'Nombre del patrocinador': e?.nombrePatrocinador,
            'Nombre del usuario': e?.nombreUsuario,
            'Correo electrónico': e?.email,
            'Sitio web': e?.website,
            Asociado: e?.registro ? 'Asociado' : 'No asociado'
          }
        });
        // console.log(this.list)
        // sessionStorage.setItem('SponsorData', JSON.stringify(this.list));
        }
        else {
          console.log(response);
          this.utilsService.dialog('ERROR', 'Alerta', '¡Evento sin datos de patrocinadores!');
        }
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', 'Error al cargar ubicaciones!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  editSponsor(sponsor_id: number) {
    this.goToPage('/admin/events/edit-patrocinadores', { ID: sponsor_id })
  }

  deletedSponsor(sponsor_id: number) {
    this.loading = true;

    this.sponsorService.deleteSponsor(sponsor_id).then(
      response => {
        console.log(response.message.title)
        console.log(response.message.description)
        this.utilsService.dialog('SUCCESS', 'Exito', 'Participante eliminado');
        this.loadDatabyEvent(this.eventID);
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Error al eliminar patrocinador!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }
}
