import { NgbDate, NgbCalendar, NgbDateParserFormatter, NgbDatepickerModule, NgbDateStruct, NgbAlertModule } from '@ng-bootstrap/ng-bootstrap';
import { Component, OnInit } from '@angular/core';
// import { KeyValue } from '@angular/common';
import { UtilsService } from 'src/app/services/utils.service';
import { ScheduleService } from 'src/app/services/schedule.service';

interface Schedule {
  id: number;
  lugar: string;
  eventId: number;
  modalidadID: number;
  nombreAgenda: string;
  fechaHoraFin: string;
  fechaHoraInicio: string;
}

@Component({
  selector: 'app-edit-agenda',
  templateUrl: './edit-agenda.component.html',
  styleUrls: ['./edit-agenda.component.css']
})

export class EditAgendaComponent {
  public eventID: number = 0;
  public scheduleID: number = 0;
  public schedules: any[] = [];
  public loading: boolean = false;
  public disabled: boolean = false;
  public dateEnd!: NgbDateStruct;
  public dateStart!: NgbDateStruct;
  public modalitySchedule: any[] = [];
  public accessToken: string | null = '';

  constructor(
    private utilsService: UtilsService,
    private scheduleService: ScheduleService
  ) { this.eventID = Number(sessionStorage.getItem('EventID')) }

  async ngOnInit() {
    const token: string | undefined = sessionStorage.getItem('Token')?.toString();
    this.scheduleID = Number(this.utilsService.getParam('ID')) || 0;

    if (token) {
      if (this.eventID > 0 && this.scheduleID == 0) {
        await this.loadModality();
        await this.loadSchedulebyEvent();
      }
      else if (this.eventID > 0 && this.scheduleID > 0) {
        await this.loadModality();
        await this.getOneSchedule(this.scheduleID);
      } else {
        this.utilsService.dialog('ERROR', 'Alerta', '¡No tienes seleccionado ningun evento!');
        this.goToPage('/admin/events/calendario-eventos');  
      }
    }
    else {
      this.utilsService.dialog('ERROR', 'Error', '¡No tienes acceso!');
      this.goToPage('/login');
    }
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName);
  }

  loadModality() {
    this.utilsService.getModalities().then(
      (response) => {
        this.modalitySchedule = response.result.modalities;
      },
      error => {
        console.log(error)
      }
    )
  }

  addSchedule() {
    this.schedules.push({
      id: 0,
      lugar: '',
      direccion: '',
      modalidadID: 0,
      nombreAgenda: '',
      horaFin: '',
      horaInicio: '',
      fechaFin: '',
      fechaInicio: '',
      fechaHoraFin: '',
      fechaHoraInicio: '',
      eventId: this.eventID,
    });
  }

  removeSchedule(i: number, schedule_id: number) {
    if (schedule_id === 0) {
      this.schedules.splice(i, 1);
    } else {
      this.scheduleService.deletedSchedule(schedule_id).then(
        (response) => { },
        error => {
          console.log(error);
          this.utilsService.dialog('ERROR', 'Error', '¡La activida no se elimino!');
        });
    }
  }

  loadSchedulebyEvent() {
    this.loading = true;
    return this.scheduleService.getScheduleByEvent(this.eventID).then(
      // return this.scheduleService.getActivityByEvent(this.eventID).then(
        response => {
        console.log(response)
        if (response.result.schedule_dates.length > 0) {
          for (const e of response.result.schedule_dates) {
            for (const i of e.schedule) {
              const tempStar =e.date.split('-'); 
              // const tempEnd = (i.fechaHoraFin.substr(0, 10)).split('-');
              // const tempStar = (i.fechaHoraInicio.substr(0, 10)).split('-');

              this.schedules.push({
                id: 'error', // i.id,
                lugar: i.description, // i.direccion,
                eventId: this.eventID,
                modalityId: 1, // i.modalityId,
                nombreAgenda: i.name, // i.nombreAgenda,
                // horaFin: i.hour.substr(0, 5), // i.fechaHoraFin.substr(11, 5),
                horaInicio: i.hour.substr(0, 5), // i.fechaHoraInicio.substr(11, 5),
                // fechaFin: { day: Number(tempEnd[2]), month: Number(tempEnd[1]), year: Number(tempEnd[0]) },
                fechaInicio: { day: Number(tempStar[2]), month: Number(tempStar[1]), year: Number(tempStar[0]) },
              });
            }
          }
          // console.log(this.schedules);
          // sessionStorage.setItem('ScheduleData', JSON.stringify(response.result));
        }
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', error); //'¡La activida no se elimino!');        
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  getOneSchedule(schedule_id: number) {
    this.loading = true;
    this.scheduleService.getOneSchedule(schedule_id).then(
      (response) => {
        // console.log(response)

        const tempEnd = (response.result.fechaHoraFin.substr(0, 10)).split('-');
        const tempStar = (response.result.fechaHoraInicio.substr(0, 10)).split('-');

        this.schedules.push({
          id: response.result.id,
          lugar: response.result.lugar, // direccion,
          direccion: response.result.lugar, // direccion,
          eventId: this.eventID,
          modalityId: response.result.modalityId,
          nombreAgenda: response.result.nombreAgenda,
          horaFin: response.result.fechaHoraFin.substr(11, 5),
          horaInicio: response.result.fechaHoraInicio.substr(11, 5),
          fechaFin: { day: Number(tempEnd[2]), month: Number(tempEnd[1]), year: Number(tempEnd[0]) },
          fechaInicio: { day: Number(tempStar[2]), month: Number(tempStar[1]), year: Number(tempStar[0]) },
        });
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡Error al cargar actividad!');
        this.goToPage('/admin/events/admin-agenda');
      }).finally(() => {
        this.loading = false
      })
  }

  async processData() {
    this.loading = true;
    this.disabled = true;

    let save: boolean = true;
    let update: boolean = true;

    for (const e of this.schedules) {
      e.lugar = e.lugar; // <-- Correcto
      e.direccion = e.lugar; // <-- Extra
      e.modalityId = Number(e.modalityId);
      e.modalidadID = Number(e.modalityId);
      e.fechaHoraFin = `${e.fechaFin.year}-${e.fechaFin.month}-${e.fechaFin.day} ${e.horaFin}`;
      e.fechaHoraInicio = `${e.fechaInicio.year}-${e.fechaInicio.month}-${e.fechaInicio.day} ${e.horaInicio}`;
      
      if (e.id === 0) {
        save = await this.save(e);
        // console.log(save)
      } else {
        update = await this.update(e);
        // console.log(update)
      }

      if (!save || !update) {
        this.loading = false;
        this.disabled = false;
        return this.utilsService.dialog('ERROR', 'Error', '¡Hubo un error al guardar!');
      } 
    }

    this.utilsService.dialog('SUCCESS', 'Exito', '¡Se guardo la informacion exitosamente!');
    this.goToPage('/admin/events/admin-agenda');

    this.loading = false;
    this.disabled = false;
  }

  async save(schedule: Schedule): Promise<boolean> {
    console.log('SAVE ', schedule);
    let status: boolean = true;
    await this.scheduleService.createSchedule(schedule).then(
      (response) => {
        console.log(response);
        status = true;
      },
      error => {
        console.log(error);
        status = false;
      });

    return status;
  }

  async update(schedule: Schedule): Promise<boolean> {
    console.log('UPDATE ', schedule);
    let status: boolean = true;

    await this.scheduleService.editSchedules(schedule.id, schedule).then(
      (response) => {
        console.log(response);
        status = true;
      },
      error => {
        console.log(error);
        status = false;
      });

    return status;
  }
}


