import { KeyValue } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { UtilsService } from 'src/app/services/utils.service';
import { ScheduleService } from 'src/app/services/schedule.service';

@Component({
  selector: 'app-admin-agenda',
  templateUrl: './admin-agenda.component.html',
  styleUrls: ['./admin-agenda.component.css']
})
export class AdminAgendaComponent implements OnInit {
  public dates: any[] = []
  public modality: any[] = [];
  public loading: boolean = false;
  public isShowDiv: boolean = true;
  public categoriesHeading: string[] = [];
  public list: {}[] = [{ 'Fecha': '', 'Hora': '', 'Lugar': '', 'Nombre de la actividad': '', 'Modalidad': '' }];

  constructor(
    private utilsService: UtilsService,
    private scheduleService: ScheduleService
  ) {
    this.categoriesHeading = Object.keys(this.list[0]);
  }


  async ngOnInit(): Promise<void> {
    this.isShowDiv = true;
    this.loading = true;
    const getModalities = await this.utilsService.getModalities();
    this.modality = getModalities.result.modalities;

    const EventID = Number(sessionStorage.getItem('EventID'));
    await this.loadSchedulebyEvent(EventID)
  }

  originalOrder = (a: KeyValue<string, string>, b: KeyValue<string, string>): number => {
    return 0;
  }

  showActions(event: any) {
    this.isShowDiv = true;
    const element = document.getElementById('item' + event.id);
    const listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if (('item' + event.id) !== listItes?.id || !listItes.id) {
      element?.classList.add('actions-disble');
    }
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName);
  }

  loadSchedulebyEvent(event_id: number) {
    this.loading = true;
    return this.scheduleService.getScheduleByEvent(event_id).then(
      response => {
        // console.log(response)
        if (response.result.schedule_dates.length > 0) {
          this.dates = [];

          this.dates = response.result.schedule_dates.map((e: any) => {
            const data = e.schedule.map((i: any) => {
              // const modality = this.modality.find(x => x.id === i.modalityId);
              return {
                Hora: i.hour.substr(0, 5), // i.fechaHoraInicio.substr(11, 5) + ' - ' + i.fechaHoraFin.substr(11, 5),
                Lugar: i.description, // i.direccion,
                'Nombre de la actividad': i.name, // i.nombreActividad,
                Modalidad: 'ERROR', // modality.name ? modality.name : '',
                id: 'ERROR' // i.id
              }
            });
            return { date: e.date, data: data }
          });
          // console.log(this.dates)
          sessionStorage.setItem('ScheduleData', JSON.stringify(response.result));
        } else {
          console.log('error 1', response);
          this.utilsService.dialog('ERROR', 'Alerta', '¡Evento sin datos de agenda!');
        }
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', 'Error al cargar agenda!');
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  editSchedule(schedule_id: number) {
    // this.utilsService.goToPage('/admin/events/edit-agenda', { ID: 16 })
    this.utilsService.goToPage('/admin/events/edit-agenda', { ID: schedule_id })
  }

  removeSchedule(schedule_id: number) {
    this.loading = true;
    this.scheduleService.deletedSchedule(schedule_id).then(
      async (response) => {
        this.utilsService.dialog('SUCCESS', 'Exito', '¡La activida se elimino!');
        const EventID = Number(sessionStorage.getItem('EventID'));
        await this.loadSchedulebyEvent(EventID);
      },
      error => {
        // console.log(error);
        this.utilsService.dialog('ERROR', 'Error', '¡La activida no se elimino!');
      }).finally(() => {
        this.loading = false
      })
  }
}
