import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BackServiceEvents } from 'src/app/models/back-service.model';
import { UtilsService } from 'src/app/services/utils.service';
import { SafeUrl } from '@angular/platform-browser';
import { NgbDateStruct, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import {
  Promotion,
  PromotionCreateModel,
  promotionCategorie,
} from 'src/app/models/promotions-models';
import { NgForm } from '@angular/forms';
import { PromotionsService } from 'src/app/services/promotions.service';
import { DatePipe } from '@angular/common';


interface dateModel {
  year: number;
  month: number;
  day: number;
}

@Component({
  selector: 'app-edit-promocion',
  templateUrl: './edit-promocion.component.html',
  styleUrls: ['./edit-promocion.component.css'],
})
export class EditPromocionComponent implements OnInit {

  public loading: boolean = false;
  public myAngularxQrCode: string = 'Promoción no guardada'
  public qrCodeSrc!: SafeUrl;
  public dateEnd!: NgbDateStruct;
  public dateStart!: NgbDateStruct;
  imageType: string = '';
  loaded: boolean = false;
  iamgeSrc: string = '';
  hasQRdata: boolean = false;
  isAdmin: boolean = false;

  aplicaLista: number[] = [];

  public promoData: PromotionCreateModel = {} as PromotionCreateModel;
  asociadoBox: boolean = false;
  noAsociadoBox: boolean = false;
  colaboradoresBox: boolean = false;
  publicoBox: boolean = false;
  promoCategories: promotionCategorie[] = [];
  limitPerUser: number[] = [];
  categorySelecte: number | undefined;
  ImageName: string = '';
  editPromoModel: Promotion | undefined;
  data: string | undefined;
  variableDePrueba: string = 'isra';
  userRole: string = sessionStorage.getItem('Role')?.toString()!;
  userIdCompany = Number(sessionStorage.getItem('companyId')?.toString()!);

  baseRoles: any = [
    {
      id: 1,
      label: 'No asociado',
      value: 'noasociado',
    },
    {
      id: 2,
      label: 'Asociado',
      value: 'asociado',
    },
    {
      id: 3,
      label: 'Colaborador',
      value: 'colaborador',
    },
    {
      id: 4,
      label: 'Aliado comercial',
      value: 'aliado_comercial',
    },
    {
      id: 5,
      label: 'Organizador eventos',
      value: 'organizador_eventos',
    },
    {
      id: 6,
      label: 'Admin',
      value: 'admin',
    },
    {
      id: 7,
      label: 'Administrador - All access',
      value: 'superadmin',
    },
  ];

  @ViewChild('AGForm') formulario!: NgForm;

  constructor(
    private utilsService: UtilsService,
    private Activatedroute: ActivatedRoute,
    private promotionService: PromotionsService,
    private router: Router
  ) { }

  ngOnInit(): void {

    this.Activatedroute.queryParams.subscribe((params) => {
      const promoModelString = params['PromoModel'];
      if (promoModelString != undefined) {
        this.editPromoModel = JSON.parse(promoModelString);
      }
    });

    this.getPromoCategories();
    this.getLimitPerUser();
    this.validateEditForm();
    this.validateIfIsAdmin()
  }

  validateIfIsAdmin() {
    if (!(this.userRole === 'ROLE_ALIADO_COMERCIAL' && this.userIdCompany != undefined)
    ) {
      this.isAdmin = true;
    }
  }


  validateEditForm() {
    if (this.editPromoModel != undefined) {
      this.promoData.id = this.editPromoModel.id;
      this.promoData.name = this.editPromoModel.name;
      this.promoData.description = this.editPromoModel.description;
      this.promoData.categoryId = this.editPromoModel.categoryId;
      this.promoData.limitUser = this.editPromoModel.limitUser;
      this.promoData.status = this.editPromoModel.status;
      this.promoData.companyId = this.editPromoModel.companyId;
      this.ImageName = this.editPromoModel.image;
      this.promoData.restrictions = this.editPromoModel.restrictions;
      this.promoData.phone = this.editPromoModel.phone;
      this.promoData.address = this.editPromoModel.address;
      this.promoData.url = this.editPromoModel.url;
      this.promoData.startDate = this.getFormatDateToTemplate(
        this.editPromoModel.startDate as string
      );
      this.promoData.finishDate = this.getFormatDateToTemplate(
        this.editPromoModel.finishDate as string
      );
      this.promoData.startHour = this.editPromoModel.startHour;
      this.promoData.finishHour = this.editPromoModel.finishHour;
      this.promoData.companyId = this.editPromoModel.companyId;
      this.aplicaLista = this.editPromoModel.roles;

      this.setInitEditRoles();

      this.getQrData();
    } else {
      this.promoData.companyId =
        this.Activatedroute.snapshot.queryParams['companyId'];
      this.promoData.status = 'active';
    }
  }

  setInitEditRoles() {
    this.asociadoBox = this.editPromoModel!.roles.includes(2);
    this.noAsociadoBox = this.editPromoModel!.roles.includes(1);
    this.colaboradoresBox = this.editPromoModel!.roles.includes(3);
    this.publicoBox = this.editPromoModel!.roles.includes(0);
  }

  async getQrData() {
    try {
      const response = await this.promotionService.getQRData(
        this.editPromoModel!.id
      );
      if (response?.success === true) {
        this.data = response.result as string;

        this.hasQRdata = true;
      } else {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        );
      }
    } catch (error) {
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'Por favor vuelve a intentar más tarde.'
      );
    }
  }

  downloadImage() {
    const link = document.createElement('a');
    link.setAttribute('type', 'hidden');
    link.href = this.data!;
    link.download = `QR promoción ${this.promoData.name}.${this.getExtension(
      this.data!
    )}`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  private getExtension(url: string): string {
    const extension = url.split(';')[0].match(/jpeg|png|gif|jpg/)![0];
    return extension;
  }

  getFormatDateToTemplate(stringDate: string) {
    const dateParts = stringDate.split('-');
    return { year: +dateParts[0], month: +dateParts[1], day: +dateParts[2] };
  }

  async getPromoCategories() {
    try {
      const response = await this.promotionService.getPromoCategories();
      if (response?.success === true) {
        this.promoCategories = response.result;

        if (this.editPromoModel != undefined) {
          this.promoData.categoryId = response.result.find(
            (x) => x.name == this.editPromoModel?.categoryName
          )?.id!;
        }
      } else {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        );
      }
    } catch (error) {
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'NO es posible cargar las categorias de promociones.'
      );
    }
  }

  async getLimitPerUser() {
    try {
      const response = await this.promotionService.getPromoLimitsPerUser();
      if (response?.success === true) {
        this.limitPerUser = response.result;
      } else {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        );
      }
    } catch (error) {
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'NO es posible cargar las limites de canjes'
      );
    }
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName);
  }

  onChangeURL(url: SafeUrl) {
    this.qrCodeSrc = url;
  }

  async onFileSelected(event: any) {
    this.loaded = false;
    const file = event.dataTransfer
      ? event.dataTransfer.files[0]
      : event.target.files[0];
    this.imageType = file.type.substring(file.type.indexOf('/') + 1);

    const reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      const fielName = event.target.files[0].name;
      if (event.target.id === 'fileGaf') {
        this.ImageName = `${event.target.files[0].name} (${event.target.files[0].size / 1000000
          } MB) `;
        this.promoData.newImage = {
          extension: this.imageType,
          data: '',
          name: fielName,
        };
        reader.onload = await this._handleReaderLoadedBadge.bind(this);
      } else if (event.target.id === 'thumbnailGafete') {
        this.promoData.newImage = {
          extension: this.imageType,
          data: '',
          name: fielName,
        };
        reader.onload = await this._handleReaderLoadedThumbnail.bind(this);
      }

      reader.readAsDataURL(file);
    }
  }

  _handleReaderLoadedBadge(e: any) {
    this.loaded = true;
    const reader = e.target;
    this.iamgeSrc = reader.result;
    this.promoData.newImage!.data = this.iamgeSrc;
  }

  _handleReaderLoadedThumbnail(e: any) {
    this.loaded = true;
    const reader = e.target;
    this.iamgeSrc = reader.result;
    this.promoData.newImage!.data = this.iamgeSrc;
  }

  onChangeBox($event: any) {
    if ($event.target.name == 'asociado' && $event.target.checked) {
      this.aplicaLista.push(2);
    } else if ($event.target.name == 'asociado' && !$event.target.checked) {
      this.aplicaLista.splice(this.aplicaLista.indexOf(2), 1);
    }
    if ($event.target.name == 'noAsociado' && $event.target.checked) {
      this.aplicaLista.push(1);
    } else if ($event.target.name == 'noAsociado' && !$event.target.checked) {
      this.aplicaLista.splice(this.aplicaLista.indexOf(1), 1);
    }
    if ($event.target.name == 'colaboradores' && $event.target.checked) {
      this.aplicaLista.push(3);
    } else if (
      $event.target.name == 'colaboradores' &&
      !$event.target.checked
    ) {
      this.aplicaLista.splice(this.aplicaLista.indexOf(3), 1);
    }
    if ($event.target.name == 'publico' && $event.target.checked) {
      this.aplicaLista.push(0);
    } else if ($event.target.name == 'publico' && !$event.target.checked) {
      this.aplicaLista.splice(this.aplicaLista.indexOf(0), 1);
    }
  }

  formatDate(enterDate: dateModel): string {
    const { year, month, day } = enterDate;
    const date = new Date(year, month - 1, day);
    const yearStr = date.getFullYear();
    const monthStr = (date.getMonth() + 1).toString().padStart(2, '0');
    const dayStr = date.getDate().toString().padStart(2, '0');
    return `${yearStr}-${monthStr}-${dayStr}`;
  }

  returnToAdminWithParam() {
    this.router.navigate(['/admin/admin-promociones'], {
      queryParams: { companyId: this.promoData.companyId },
    });
  }

  async createPromo() {
    this.promoData.roles = this.aplicaLista;
    //  VALOR QUEMADO PARA PRUEBAS YA QUE NO SE HA DEFINIDO LÓGICA DE LIMITES DE USUARIO Y CANJES

    if (this.formulario.valid) {
      this.promoData.limit = 10;
      this.promoData.limitUser = Number(this.promoData.limitUser);
      this.promoData.categoryId = Number(this.promoData.categoryId);
      this.promoData.startDate = this.formatDate(
        this.promoData.startDate as dateModel
      );
      this.promoData.finishDate = this.formatDate(
        this.promoData.finishDate as dateModel
      );

      if (!this.isStartDateBeforeFinishDate()) {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'La fecha de finalización debe ser mayor a la fecha de inicio.'
        );
        return
      }



      try {
        this.loading = true;
        if (this.editPromoModel != undefined) {
          var response = await this.promotionService.updatePromotion(
            this.promoData
          );
        } else {

          if (this.isAdmin) {

            var response = await this.promotionService.createPromotionByAdminRole(
              this.promoData
            );
          } else {

            var response = await this.promotionService.createPromotionByComercialRole(
              this.promoData
            );
          }

        }

        console.log(response);

        if (response?.success === true) {
          this.utilsService.dialog(
            'SUCCESS',
            'Notificación',
            response?.message.description
          );
          this.returnToAdminWithParam();
        } else {
          this.loading = false;
          this.utilsService.dialog(
            'ERROR',
            'Error',
            response?.message.description ??
            'Por favor vuelve a intentar más tarde.'
          );
        }
      } catch (error) {
        this.loading = false;
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'NO es posible crear la promoción, revisa los datos ingresados y vuelve a intentar'
        );
      }
    } else {
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'Por favor llena los campos obligatorios.'
      );
    }
  }


  isStartDateBeforeFinishDate(): boolean {
    const startDateTime = `${this.promoData.startDate} ${this.promoData.startHour}`
    const finishDateTime = `${this.promoData.finishDate} ${this.promoData.finishHour}`
    const start = Date.parse(startDateTime);
    const finish = Date.parse(finishDateTime);

    console.log(start, finish);
    console.log(startDateTime, finishDateTime);


    if (start < finish) {
      return true;
    } else {
      return false;
    }
  }

  formatSelectedDate(date: string | NgbDateStruct): string {
    const dateAsStruct = date as NgbDateStruct
    if (date) {
      return `${dateAsStruct.day} de ${this.getMonthName(dateAsStruct.month)} del ${dateAsStruct.year}`;
    } else {
      return '';
    }
  }

  getMonthName(monthNumber: number): string {
    let monthName: string;
    switch (monthNumber) {
      case 1:
        monthName = "enero";
        break;
      case 2:
        monthName = "febrero";
        break;
      case 3:
        monthName = "marzo";
        break;
      case 4:
        monthName = "abril";
        break;
      case 5:
        monthName = "mayo";
        break;
      case 6:
        monthName = "junio";
        break;
      case 7:
        monthName = "julio";
        break;
      case 8:
        monthName = "agosto";
        break;
      case 9:
        monthName = "septiembre";
        break;
      case 10:
        monthName = "octubre";
        break;
      case 11:
        monthName = "noviembre";
        break;
      case 12:
        monthName = "diciembre";
        break;
      default:
        monthName = "mes inválido";
        break;
    }
    return monthName;
  }


}
