import { Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
// import { Component, Input, OnInit, ViewEncapsulation } from '@angular/core';
import { GalleryService } from 'src/app/services/gallery.service';
import { BackServiceGallery } from 'src/app/models/back-service.model';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-image-dialog',
  templateUrl: './image-dialog.component.html',
  styleUrls: ['./image-dialog.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class ImageDialogComponent {
  public loaded: boolean = false;
  public loading: boolean = false;
  public imageLoaded: boolean = false;

  @Input() type!: string;
  @Input() gallery!: BackServiceGallery;
  @Output() response: EventEmitter<any> = new EventEmitter();

  constructor(
    private modalService: NgbModal,
    private galleryService: GalleryService
  ) { 
    // if(this.type === 'NEW'){ 
    //   this.gallery.file = '';
    // }
  }

  // ngOnInit(): void { }

  onFileSelected(event: any) {
    if (event.target.files && event.target.files.length > 0) {
      this.gallery.fileName = event.target.files[0].name;
    }
    else {
      this.gallery.fileName = "No hay archivos seleccionados.";
    }

    const file = event.dataTransfer ? event.dataTransfer.files[0] : event.target.files[0];
    this.gallery.type = file.type.substring(file.type.indexOf('/') + 1);

    const reader = new FileReader();
    this.loaded = false;

    reader.onload = this._handleReaderLoaded.bind(this);
    reader.readAsDataURL(file);
  }

  _handleReaderLoaded(e: any) {
    this.loaded = true;
    const reader = e.target;
    this.gallery.file = reader.result;
  }

  async save() {
    this.loading = true;
    console.log(this.gallery)
    this.gallery.dateCreated = new Date();
    await this.galleryService.addGalleryToEvent(this.gallery).then(
      response => {
        console.log(response)
        // this.response.emit('SUCCESS');
        this.modalService.dismissAll(this);
        this.response.emit({type: 'SUCCESS', title: 'Exito', message: 'Imagen guardada.' });
      },
      error => {
        console.log(error)
        // this.response.emit('FALSE');
        this.modalService.dismissAll(this);
        this.response.emit({type: 'ERROR', title: 'Error', message: '¡Error al cargar galeria!'});
      }
    ).finally( () => {
      this.loading = false;
      this.gallery.type = '';
      this.gallery.file = '';
      this.gallery.fileName = '';
    });

    // this.modalService.dismissAll(this);
  }

}
