import { Component, OnInit, Output } from '@angular/core';
import { BackService } from '../models/back-service.model';
import { BackofficeService } from '../services/backoffice.service';
import { Router } from '@angular/router';
import { ErrorDialogComponent } from 'src/app/Extra/error-dialog/error-dialog.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-password-recovery',
  templateUrl: './password-recovery.component.html',
  styleUrls: ['./password-recovery.component.css']
})
export class PasswordRecoveryComponent {

  email: string = '';
  loginData: BackService = {
    username: '',
    password: ''
  };

  @Output() toRecover?: string;

  constructor(private router: Router, private backofficeService: BackofficeService, private modalService: NgbModal) { }

  recovery() {
    this.loginData = {
      username: this.email
    }

    this.backofficeService.recovery(this.loginData)
      .subscribe({
        next: (res) => {
          console.log('true', res)
          sessionStorage.setItem('tempToken', res.result.token);
          sessionStorage.setItem('recoverEmail', this.email);
          this.goToPage('/code-recovery');
        },
        error: (e) => {
          console.log('false', e)
          const modalRef = this.modalService.open(ErrorDialogComponent, { centered: true })
          modalRef.componentInstance.errorMessage = "El usuario ingresado no es válido.";
        }
      })
  }

  goToPage(pageName: string) {
    this.router.navigate([`${pageName}`]);
  }

}
