  async onFileSelected(event: any) {
    const file = event.target.files[0];
    const extension = file.type.split('/')
    const base64 = await this.utilsService.convertBase64(event.target.files.item(0));

    if (typeof (base64) === 'string') {
      switch (event.target.id) {
        case '_standImageLogotipo':
          this.stand.imageLogotipo = { extension: extension[1], name: file.name, data: base64 }
          break;
        case '_imageFloorOne':
          this.map.imageFloorOne = { extension: extension[1], name: file.name, data: base64 }
          break;
        case '_imageFloorTwo':
          this.map.imageFloorTwo = { extension: extension[1], name: file.name, data: base64 }
          break;
        case '_imageFloorThree':
          this.map.imageFloorThree = { extension: extension[1], name: file.name, data: base64 }
          break;
        case '_imageFloorFour':
          this.map.imageFloorFour = { extension: extension[1], name: file.name, data: base64 }
          break;
      }
    }
  }

