import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
    providedIn: 'root'
})

export class GalleryService {
    private headers: {} = {};
    private url: string = environment.url;
    private accessToken: string | undefined;

    constructor(private http: HttpClient) {
        this.accessToken = sessionStorage.getItem('Token')?.toString();

        this.headers = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'accept': 'application/json',
                'x-access-token': this.accessToken!
            })
        };
    }

    // Obtener imagenes del evento
    getGalleryByEvent(event_id: number): Promise<any> {        
        // console.log('service')                                      
        return this.http.get(`${this.url}gallery/media/?idEvent=${event_id}`, this.headers).toPromise();
    }

    // Obtener una imagen
    // getOneGallery(gallery_id: number): Promise<any> {
    //     return this.http.get(`${this.url}eventos/gallerys/${gallery_id}`, this.headers).toPromise();
    // }

    // Agregar una imagen
    addGalleryToEvent(data: any): Promise<any> {
                                        // gallery/media/null
        return this.http.post(`${this.url}/gallery/media/`, data, this.headers).toPromise();
    }

    // Actualiar una imagen de un evento
    // updateGalleryToEvent(ID: number, data: any): Promise<any> {
    //     return this.http.put(`${this.url}eventos/gallerys/${ID}`, data, this.headers).toPromise();
    // }

    // Eliminar una imagen de un evento
    // deleteGallery(ID: number): Promise<any> {
    //     return this.http.delete(`${this.url}eventos/gallerys/${ID}`, this.headers).toPromise();
    // }
}
